<template>
  <div class="noContent">
    <div class="spinner">
      <div class="double-bounce1"></div>
      <div class="double-bounce2"></div>
    </div>
    {{name}}
  </div>
</template>
<script>
export default {
  props: ["loadName"],
  data() {
    return {};
  },
  computed: {
    name() {
      return this.loadName + "加载中 Loading...";
    }
  }
};
</script>
<style scoped>
div.noContent {
  min-height: 300px;
  width: 100%;
  overflow: hidden;
  text-align: center;
  background: #fafafa;
}
div.noContentBox {
  min-height: 330px;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.spinner {
  width: 60px;
  height: 60px;

  position: relative;
  margin: 100px auto;
}

.double-bounce1,
.double-bounce2 {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #5cb6e8;
  opacity: 0.6;
  position: absolute;
  top: 0;
  left: 0;
  animation: bounce 2s infinite ease-in-out;
}

.double-bounce2 {
  -webkit-animation-delay: -1s;
  animation-delay: -1s;
}
@keyframes bounce {
  0%,
  100% {
    transform: scale(0);
    -webkit-transform: scale(0);
  }
  50% {
    transform: scale(1);
    -webkit-transform: scale(1);
  }
}
</style>





