<template>
  <div id="Policy">
    <div id="content—container">
      <!--左边长列表-->
      <div class="box1-left">
        <currentLocate></currentLocate>
        <div class="box2-left">
          <main-list :queryProp="query"></main-list>
        </div>
      </div>
      <!--右边长列表-->
      <div class="box4-right">
        <div class="box5-right">
          <keep-alive>
            <pb-list :widgetMessage="tabs2[1]" :widgetType="1"></pb-list>
          </keep-alive>
        </div>
        <div class="box6-right">
          <user-box></user-box>
        </div>
        <div class="box7-right">
          <keep-alive>
            <pb-list :widgetMessage="tabs2[0]" :widgetType="1"></pb-list>
          </keep-alive>
        </div>
        <div class="box12-right">
          <ul class="title">
            <li class="list_title">
              <a style="margin-left:15px;" class="title-active">广告招收栏目</a>
            </li>
          </ul>
          <div style="height:270px;width:100%;">
            <keep-alive>
              <slide2></slide2>
            </keep-alive>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

 <script>
import UserBox from "@/components/common/tools/user-box";
import slide2 from "@/components/common/tools/slide2";
import currentLocate from "@/components/common/tools/currentLocate";
import PbList from "@/components/common/tools/pb-list";
import mainList from "@/components/index/content/main-list";
import { proofreading } from "@/api/getData";

export default {
  name: "Policy",
  data() {
    return {
      query: {
        message: {
          name: "政策",
          belong: "资讯",
          path: "/news_3", //点击跳转
          type: 3, //类别：两图+列表
          requestPath: "/information/findPolicyList", //文章列表url
          newsUrl: "/information/findPolicyInfo" //具体文章请求url
        }
      }
    };
  },
  computed: {
    tabs2() {
      let value = global.tabs;
      let value1, value2;
      value1 = value.filter(item => {
        if (item.belong == "活动") return true;
      });
      value2 = value.filter(item => {
        if (item.belong == "相关热点") return true;
      });
      if (!value[0].message && !value[0].message && !value[0].message) {
        proofreading.call(this); //数据未加载，调用函数校对加载
      }
      value = [value1, value2];
      return value;
    }
  },
  methods: {
    init() {
      //根据传参判断路由显示路径
      let localName = this.query.message.name;
      let localMenu = this.query.message.belong;
      let index = this.$router.options.routes[0].children.findIndex(
        menu => menu.showName == localMenu
      );
      let index2;
      global.currentPath.path = localName;
      // console.log(localMenu);
      if (index != -1) {
        index2 = this.$router.options.routes[0].children[
          index
        ].children.findIndex(menu => menu.showSecondName == localName);
        global.currentPath.data = [index, index2];
      } else {
        global.currentPath.data = [index];
      }
    }
  },
  activated() {
    this.init();
  },
  components: {
    UserBox,
    slide2,
    PbList,
    mainList,
    currentLocate
  }
};
</script>
<style lang="scss" scoped>
#content—container /deep/ img {
  max-width: 650px;
}
@mixin adv {
  margin: 5px auto;
  width: 1200px;
  height: 125px;
  overflow: hidden;
}
@mixin w-box {
  margin: 5px auto;
  width: 1200px;
  height: 312px;
  overflow: hidden;
  margin-bottom: 18px;
}
@mixin i-box {
  box-shadow: 0 0 1px #bebebe;
  height: 100%;
  overflow: hidden;
  border-radius: 2px;
}
@mixin border {
  -webkit-box-shadow: 0 0 1px #bebebe;
  box-shadow: 0 0 1px #bebebe;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border: 1px solid rgba(204, 204, 204, 0.377);
}
#content—container {
  @include adv;
  height: 100%;
}
/*右边小框重复样式*/
@mixin litter_one_tab_box {
  @include i-box;
  @include border;
  height: 312px;
  width: 390px;
  float: right;
  margin-bottom: 10px;
}
.hot_search {
  @include adv;
  height: 100%;
  //margin-left:15px;
  padding: 13px;
  background: rgba(255, 255, 255, 0.95);
}
.box1-left {
  margin-top: 20px;
  width: 790px;
  float: left;
  height: 100%;
  overflow: hidden;
  border-radius: 2px;
  margin-bottom: 20px;
}
.box2-left {
  margin: 10px 0;
}

/*左边文章样式*/
.box3-left {
  //@include i-box;
  height: 100%;
  margin: 10px 0;
  background: rgba(255, 255, 255, 0.95);
}
.box4-right {
  margin-top: 20px;
  width: 390px;
  //@include i-box;
  float: right;
}
.box4-right div {
  margin-right: 0.5px;
}
.box12-right,
.box5-right,
.box6-right,
.box7-right {
  @include litter_one_tab_box;
  background: rgba(255, 255, 255, 0.95);
}
.box8-left {
  //@include i-box;
  float: left;
  width: 795px;
  margin: 10px 0;
}
.box9-left {
  @include i-box;
  float: left;
  width: 795px;
  height: 250px !important;
  margin: 10px 0;
}
.box10-left {
  //@include i-box;
  float: left;
  width: 795px;
  height: 50px;
  margin: 10px 0;
}
.box11-left {
  //@include i-box;
  float: left;
  width: 795px;
  height: 400px;
  margin: 10px 0;
}
//行业报告的形式
.image {
  margin: 0 auto;
  width: 200px;
  margin-left: 300px;
}
.Industry_Report_span_style {
  float: right;
  margin-right: 200px;
}
.Industry_Report_span0_style {
  float: right;
}
#Industry_Report_a {
  text-decoration: underline;
  color: #127de7;
}
#Industry_Report_a:hover {
  text-decoration: underline;
  color: red;
}
.title {
  margin-bottom: 10px;
  margin-top: 2px;
  width: 100%;
  background: #fff;
  height: 32px;
  line-height: 28px;
}
</style>



