<template>
  <div id="main-list-meeting">
    <!-- 加载框 -->
    <div v-if="loading">
      <h1 id="bigTitle">列表正在加载... loading...</h1>
      <noContent :loadName="'列表'"></noContent>
    </div>

    <!-- 存放列表数据的框 -->
    <div v-show="!loading" id="list-box">
      <!-- 列表 -->
      <div class="lists">
        <li v-for="(item,index) in currentList" :key="index">
          <div class="img">
            <img :src="item.coverImg">
          </div>
          <div class="item">
            <div class="title" @click.stop="routerLink(item.id)">{{item.title}}</div>
            <div class="time">时间：{{item.postDate.toString().slice(0,10)}}</div>
            <div class="location">地点：{{item.address}}</div>
            <div class="host">举办方：{{item.host}}</div>
            <div id="sign" v-if="sign">点我报名</div>
            <!-- <div class="line"></div> -->
            <div class="message" title="view more">{{item.introduction}}</div>
            <!-- <div class="line"></div> -->
          </div>
        </li>
      </div>
      <!-- 分页组件 -->
      <div class="pagination">
        <el-pagination background layout="prev, pager, next" :total="parseInt(total)" :page-size="pageSize" :current-page.sync="currentPage">
        </el-pagination>
      </div>
    </div>

  </div>
</template>

<script>
import { getContentList } from "@/api/getData";
import noContent from "@/components/index/content/no-content";

export default {
  props: ["queryProp"],
  name: "main-list-meeting",
  data() {
    return {
      queryMsg: "",
      currentList: "",
      total: 1,
      pageSize: 12,
      currentPage: 1,
      hostUrl: "", //请求地址
      loading: true,
      sign: true
    };
  },
  methods: {
    //跳转
    routerLink: function(id) {
      if (id) {
        // let path =
        //   this.message[this.currentIndex].path +
        //   this.message[this.currentIndex].path +
        //   "_" +
        //   childrenPath;
        let path = "content";
        let query = {
          message: this.queryMsg,
          id: id
        };
        let data = JSON.stringify(query); ///利用JSON传输，避免刷新后出现[Object,Object]的情况
        this.$router.push({ path: path, query: { queryMsg: data } });
      }
    },
    //获取列表
    getData() {
      let that = this;
      let url = this.hostUrl;
      let pageNum = that.currentPage;
      that.loading = true;
      getContentList(url, pageNum)
        .then(res => {
          let data = res.message;
          if (data) {
            that.loading = false;
            that.currentList = data.list;
            that.total = data.total;
            that.currentPage = data.pageNum;
            that.pageSize = data.pageSize;
            if (!data.list) {
              that.currentList = [
                {
                  id: 1,
                  userId: null,
                  coverImg:
                    "https://images.ofweek.com/Upload/seminar/2018-9/201891915127782.png",
                  title: "测试标题",
                  postDate: "2018-11-22 22:06:27.0",
                  address: 34,
                  host: 345,
                  introduction: 123213213,
                  type: null
                },
                {
                  id: 1,
                  userId: null,
                  coverImg:
                    "https://images.ofweek.com/Upload/seminar/2018-9/201891915127782.png",
                  title: "测试标题2",
                  postDate: "2019-11-22 22:06:27.0",
                  address: 123,
                  host: 321,
                  introduction: 12312313212,
                  type: null
                }
              ];
              that.total = 2;
              that.currentPage = 1;
              that.pageSize = 2;
            }
          }
        })
        .catch(err => {
          that.loading = false;
          that.$message.error({
            showClose: true,
            message: "获取文章列表错误",
            duration: 2000
          });
        });
    },
    //更改页数
    handleCurrentChange(val) {
      this.currentPage = val;
      this.getData();
    }
  },
  watch: {
    queryProp: {
      handler(val) {
        if (val) {
          this.queryMsg = this.queryProp.message;
          if (this.queryMsg.requestPath) {
            this.hostUrl = this.HOST + this.queryMsg.requestPath; //拼接请求路径
            this.getData(); //调用获取文章列表方法
          }
        }
      },
      immediate: true,
      deep: true
    }
  },
  components: { noContent }
};
</script>

<style lang="scss" scoped>
$blue: #127de7;
.title:hover > a {
  color: $blue;
}
#main-list-meeting {
  // margin-top: 16px;
  background: #f6f6f6;
  border-radius: 2px;
}
h1#bigTitle {
  text-align: center;
  padding: 30px;
}
.lists {
  & {
    padding: 5px 10px;
    box-sizing: border-box;
  }
  li {
    & {
      overflow: hidden;
      zoom: 1;
      margin-bottom: 10px;
      -webkit-box-shadow: 0 0 1px #bebebe;
      box-shadow: 0 0 1px #bebebe;
    }
    &::after {
      display: block;
      clear: both;
      content: "";
      visibility: hidden;
      height: 0;
    }
    .img {
      & {
        width: 46%;
        float: left;
        height: 250px;
        overflow: hidden;
      }
      img {
        width: 100%;
        height: 100%;
      }
    }
    .item {
      & {
        padding: 20px;
        box-sizing: border-box;
        width: 54%;
        float: right;
        height: 250px;
        color: rgb(68, 68, 68);
        overflow: hidden;
        position: relative;
      }
      .title {
        text-align: center;
        font-weight: 600;
        font-size: 20px;
        color: #d45b15;
        padding: 0;
        margin-bottom: 5px;
        cursor: pointer;
      }
      div {
        & {
          padding: 5px;
        }
      }
      #sign {
        position: absolute;
        top: 103px;
        right: 40px;
        color: #e65e10;
        cursor: pointer;
      }
      #sign:hover {
        color: #d42215;
      }
      .message {
        & {
          overflow: hidden;
          text-overflow: ellipsis;
          // white-space: nowrap;
          height: 80px;
          margin: 5px 0 10px 15px;
          display: -webkit-box;
          -webkit-line-clamp: 4; /*可指定任一行显示...效果*/
          -webkit-box-orient: vertical;
          letter-spacing: 2px;
          word-spacing: 2px;
          cursor: pointer;
        }
        &:hover {
          color: #e66f2a;
        }
      }
      .line {
        width: 93%;
        margin: 0 auto;
        content: "";
        padding: 2px;
        border-bottom: rgb(158, 158, 158) 1px dotted;
      }
    }
  }
}
</style>



