<!--
 * @Description: 定位信息组件，利用全局的global.currentPath属性配合路由作导航和跳转
 -->
<template>
  <div id="locatePath">
    <div class="leftBox">
      <span id="backHome" class="select" @click="routerPath('/home')">
        <i class="fa" v-bind:class="menus[1].icon"></i> 服务平台
      </span>
      <span>现在位置 :</span>
      <span class="select" @click.stop="routerPath(menus[currentPath[0]].path)">
        <i class="fa" :class="currentPath[0]!=-1?menus[currentPath[0]].icon : ''"></i>
        {{currentPath[0]!=-1?menus[currentPath[0]].showName:simplePath}}
      </span>
      <span v-if="currentPath.length>1">
        :
        <span class="active select" @click.stop="routerPath(menus[currentPath[0]].children[currentPath[1]].path)">{{menus[currentPath[0]].children[currentPath[1]].showSecondName}}</span>
      </span>
    </div>
  </div>
</template>

<script>
import eventBus from "@/common/eventBus";

export default {
  name: "locatePath",
  data() {
    return {
      menus: this.$router.options.routes[0].children,
      currentPath: global.currentPath.data,
      simplePath: ""//用于显示路由中没有的单独路径
    };
  },
  methods: {
    routerPath: function(path) {
      this.$router.push(path);
    }
  },
  mounted() {
    let that = this;
    eventBus.$on("SynchronizePath", function(val) {
      that.currentPath = val;
      if (val[1] == -1) {
        that.currentPath = that.currentPath.slice(0, 1);
      }
      if (val[0] == -1) {
        that.simplePath = global.currentPath.path;//路由中找不到相应路径，从全局currentPath的path中直接获取
      }
    });
  },
  // activated() {
  //   this.currentPath = global.currentPath.data;
  //   console.log(this.currentPath)
  // },
  created() {}
};
</script>

<style lang="scss" scoped>
$blue: #1460ac;
.select:hover {
  cursor: pointer;
  color: $blue;
}
#backHome {
  padding-right: 15px;
  margin-right: 15px;
  border-right: 2px solid rgb(64, 66, 68);
  cursor: pointer;
  position: relative;
}
div#locatePath {
  background: #f6f6f6;
  font-size: 17px;
  padding: 10px 20px;
  font-weight: 600;
  border: 1px solid #e6e6e6;
  box-sizing: border-box;
  color: #469
}
</style>



