<template>
  <div class="pb-list">
    <!-- 小部件的标题 -->
    <ul class="title" :class="{'no-fa':widgetType==1||widgetType==2}">
      <li class="belong-title" v-if="!widgetType">
        <i class="fa fa-title" :class="message[0].fa" aria-hidden="true"></i>
        <span>{{message[0].belong}}</span>
      </li>
      <li class="widget-title" v-for="(item,index) in message" :key="index" @mouseover="currentIndex = index" @click.stop="routerPath(item)">
        <a :class="{'title-active':currentIndex == index}">{{item.name}}</a>
      </li>
      <li v-if="widgetType==2" class="more-list" @click.stop="routerPath(message[0])">
        <a>更多</a>
      </li>
    </ul>
    <div class="boxLoading" v-if="content[0].title == '传入数据为空'&& message[0].name!='精彩展会'&&message[0].name!='热门PPT'"></div>
    <!-- 小部件显示内容 -->
    <div class="widget-content-box" v-if="content[0].title != '传入数据为空'||message[0].name=='精彩展会'||message[0].name=='热门PPT'">
      <!-- type1 纯文章 -->
      <div v-show="type==1">
        <div class="normal-list">
          <ul>
            <li class="contentLi" v-for="(item,index) in fakeContent" :key="index">
              <a :title="item.title" class="contentNoTimeTitle" @click.stop="query.message=Object.assign({},message[currentIndex]),query.id=item.id,routerLink('content')">{{item.title}}</a>
            </li>
          </ul>
        </div>
      </div>
      <!-- type2 轮播+文章-->
      <div v-show="type==2">
        <div class="slide">
          <slide :myMessage='content'></slide>
        </div>
        <div class="widget-list" style="width:55%;">
          <ul>
            <li class="contentLi" v-for="(item,index) in content.slice(0,8)" :key="index">
              <a :title="item.title" class="contentWithTimeTitle" @click.stop="query.message=Object.assign({},message[currentIndex]),query.id=item.id,routerLink('content')">{{item.title}}</a>
              <b>{{item.postDate}}</b>
            </li>
          </ul>
        </div>
      </div>
      <!-- type3 2静态图+文章-->
      <div v-show="type==3">
        <div class="double-pics">
          <div v-for="(item, index) in filterContent" :key="index" v-if="index<2">
            <img :src="item.img==null&&index==0?'http://www.gdsjqr.com/attachment/cms/item/2018_09/10_09/9be272872aff037c.jpg':item.img==null&&index==1?'http://www.gdsjqr.com/attachment/cms/item/2018_09/12_08/efabfe0282717917.jpg':item.img" alt="" @click.stop="query.message=Object.assign({},message[currentIndex]),query.id=item.id,routerLink('content')">
            <p :title="item.title" @click.stop="query.message=Object.assign({},message[currentIndex]),query.id=item.id,routerLink('content')">{{item.title}}</p>
          </div>
        </div>
        <div class="widget-list" style="width:540px;">
          <ul>
            <li class="contentLi" v-for="(item,index) in content.slice(0,8)" :key="index">
              <a :title="item.title" class="contentWithTimeTitle" @click.stop="query.message=Object.assign({},message[currentIndex]),query.id=item.id,routerLink('content')">{{item.title}}</a>
              <b>{{item.postDate}}</b>
            </li>
          </ul>
        </div>
      </div>
      <!-- type4 8张图-->
      <div v-show="type==4" style="height: 270px; width: 100%;">
        <div class="eight-pics">
          <ul>
            <li v-for="(item, index) in content" v-if="index<8" :key="index">
              <img :src='item.logo' @click.stop="query.message=Object.assign({},message[currentIndex]),query.id=item.id,routerLink('content')">
              <p @click.stop="query.message=Object.assign({},message[currentIndex]),query.id=item.id,routerLink('content')">{{item.name}}</p>
            </li>
          </ul>
        </div>
      </div>
      <!-- type5 专家高校 -->
      <div v-show="type==5">
        <div class="person-list">
          <div v-for="(item,index) in filterContent" :class="index/2?'r_list':'l_list'" :key="index">
            <p class="perpon-name" @click.stop="query.message=Object.assign({},message[currentIndex]),query.id=item.id,routerLink('content')">
              {{item.title}}
            </p>
          </div>
        </div>
      </div>
      <!-- type6 协会之窗 -->
      <div v-show="type==6">
        <div id="association-introduce">
          <div class="img">
            <img src="@static/PICS/association.png" alt="">
          </div>
          <div class="content">
            <div class="name"><span>【名称】</span>广东省机器人协会</div>
            <div class="time"><span>【成立时间】</span>2015年5月16日</div>
            <div class="introduction"><span>【简介】</span><br>广东省机器人协会是由广东省内从事机器人相关设备设计、制造、应用的大专院校、科研机构和生产、销售的企事业单位以及从事相同性质经济活动的经济组织等自愿发起组建的全省性、专业性、非营利性并具有独立法人资格的社会团体。<span class="more">【详细信息】</span></div>
            <div class="boss">
              <span>【轮值会长】</span>杨煜俊
              <span>【会长】</span>孙志强
              <span>【执行会长】</span>任玉桐
            </div>
          </div>
        </div>
      </div>
      <!-- type7 轮播+会员 -->
      <div v-show="type==7">
        <div class="slide">
          <slide :my-message="filterContent" :currentTab="message[currentIndex].name"></slide>
        </div>
        <div class="person-list" style="float:right; width: 55%;">
          <p v-for="(item,index) in filterContent" style="width:400px" class="perpon-name" :key="index" @click.stop="query.message=Object.assign({},message[currentIndex]),query.id=item.id,routerLink('content')">
            {{item.title}}
          </p>
        </div>
      </div>
      <!-- type8 纯文本+日期 -->
      <div v-show="type==8">
        <div class="widget-list" style="width:100%;">
          <ul>
            <li class="contentLi" v-for="(item,index) in content.slice(0,8)" :key="index">
              <a :title="item.title" class="contentWithTimeTitle" @click.stop="query.message=Object.assign({},message[currentIndex]),query.id=item.id,routerLink('content')">{{item.title}}</a>
              <b>{{item.postDate}}</b>
            </li>
          </ul>
        </div>
      </div>
      <!-- type9 三图+文本 -->
      <div v-show="type==9">
        <div class="three-pics">
          <div class="list_left">
            <div class="list_top">
              <li class="list_title" v-for="(item, index) in content.slice(0,4)" :key="index" @click.stop="query.message=Object.assign({},message[currentIndex]),query.id=item.id,routerLink('content')">
                {{item.title}}
              </li>
            </div>
            <div class="list_bottom">
              <li class="list_title" v-for="(item, index) in content.slice(4,9)" :key="index" @click.stop="query.message=Object.assign({},message[currentIndex]),query.id=item.id,routerLink('content')">
                {{item.title}}
              </li>
            </div>
          </div>
          <div class="list_right">
            <div class="list_top">
              <div :key="index" v-for="(pic, index) in filterContent" v-if="index<3" @click.stop="query.message=Object.assign({},message[currentIndex]),query.id=item.id,routerLink('content')">
                <img :src='pic.img'>
                <span class="product-title" :title="pic.title">{{pic.title}}</span>
              </div>
            </div>
            <div class="list_bottom">
              <li class="list_title" v-for="(item, index) in content.slice(10,15)" :key="index" @click.stop="query.message=Object.assign({},message[currentIndex]),query.id=item.id,routerLink('content')">
                {{item.title}}
              </li>
            </div>
          </div>
        </div>
      </div>
      <!-- type10 最新招聘 -->
      <div v-show="type==10">
        <div class="job-list">
          <ul>
            <li v-for="(item, index) in content.slice(0,8)" :key="index">
              <a class="list_title" @click="query.id=item.id,query.message=Object.assign({},message[currentIndex]),routerLink('/recruitment_1')"> {{item.title}} </a>
              <b class="pay" style="color: #gf971e;">{{item.salary}}</b>
              <b class="edu_record">{{item.degreeRequired}}</b>
              <b class="duration">{{item.workAge}}</b>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
/*接受两个参数 一个是widgetName数组，一个是widgetMessage数组，widgetMessage数组的每个元素为该组件下导航nav对应的内容，
  包括：name小版块名称，belong该板块归属（该组件归属）path点击板块标题跳转的路径，massage数组保存该板块的内容,
  type保存显示小部件的样式类型:1位纯文章类文本，2为轮播图+文本，3为静态图+文本，4为8图结构，5为专家高校,6为协会之窗，7为轮播图+会员，8为文本+日期，9为三图+文本，10位最新招聘等*/
/*message存储的数组元素的数组元素的标题属性为title,封面属性为img,时间属性为postDate*/
/*占据整行,最小高度为399px 专业逼死强迫症*/
/*后续扩展，再接受一个widgetType变量，改变widget的种类*/

import Slide from "@/components/common/tools/slide";
export default {
  props: ["widgetMessage", "widgetType"],
  name: "list",
  data() {
    return {
      query: new Object(),
      //首页传过来的数据
      message: this.widgetMessage || [
        {
          name: "",
          belong: "资讯", //归属的小标题
          path: "/news", //点击跳转
          message: [{ title: "传入数据为空" }], //组件内容，对象数组
          type: 0,
          requestPath: "/", //文章列表url
          newsUrl: "/" //具体文章请求url
        }
      ],
      currentIndex: 0,
      //显示的部件类型
      type: this.widgetMessage[0].type || 1,
      //当前显示的数据
      content: this.widgetMessage[0].message || [{ title: "传入数据为空" }]
    };
  },
  computed: {
    filterContent() {
      //二图格式把图片放到前面
      let that = this;
      if (this.type == 3) {
        let picDatas = that.content.filter(function(item) {
          return item.img != null && item.img != "";
        });
        let noPicData = that.content.filter(function(item) {
          return item.img == null || item.img == "";
        });
        let data = picDatas.concat(noPicData);
        return data;
      }
      //专家高校添加title属性
      if (this.type == 5) {
        return that.content.map(function(item, index) {
          item.title = item.name;
          return item;
        });
      }
      //协会会员产品库添加title+排序
      if (this.type == 7) {
        let picDatas = that.content.filter(function(item) {
          return item.img != null && item.img != "";
        });
        let noPicData = that.content.filter(function(item) {
          return item.img == null || item.img == "";
        });
        let data = picDatas.concat(noPicData);
        return data.map(function(item, index) {
          item.title = item.name;
          return item;
        });
      }
      //三图模式
      if (this.type == 9) {
        let data = that.content.filter(function(item) {
          return item.img != null && item.img != "";
        });
        if (data == "" || data == null) {
          data = [
            {
              img:
                "http://122.13.4.239/resources/img/industry_information/5aa896a2d4ea5363.jpg",
              title: "",
              id: ""
            },
            {
              img:
                "http://122.13.4.239/resources/img/industry_information/5aa896a2d4ea5363.jpg",
              title: "",
              id: ""
            },
            {
              img:
                "http://122.13.4.239/resources/img/industry_information/5aa896a2d4ea5363.jpg",
              title: "",
              id: ""
            }
          ];
        }
        return data;
      }
    },
    //假数据
    fakeContent() {
      if (this.widgetType == 1) {
        let that = this;
        let currentIndex = this.currentIndex;
        let data = [];
        switch (that.message[currentIndex].name) {
          case "热门PPT":
            data = [
              {
                title: "OFweek大会李聪廷：安防AI应用现状与展望",
                time: "2018-09-20"
              },
              {
                title: "OFweek（第二届）中国人工智能产业大会",
                time: "2018-09-20"
              },
              {
                title: "OFweek大会陈吉胜：面向AIoT的人机交互云端芯演进之路",
                time: "2018-09-20"
              },
              {
                title: "OFweek（第二届）中国人工智能产业大会",
                time: "2018-09-20"
              },
              {
                title: "OFweek大会孙力：嵌入式人工智能的挑战与实践 v1.0",
                time: "2018-09-20"
              },
              {
                title: "OFweek（第二届）中国人工智能产业大会",
                time: "2018-09-20"
              },
              {
                title: "OFweek（第二届）中国人工智能产业大会-AI+医疗论坛",
                time: "2018-09-20"
              },
              {
                title: "OFweek大会田广野：AI开启数字化医疗创新新模式",
                time: "2018-09-20"
              }
            ];
            break;
          case "精彩展会":
            data = [
              {
                title: "中国LED植物照明发展前景及面临的难题分析",
                time: "2018-09-20"
              },
              {
                title: "“一带一路”战略好处多 沿线成LED出口新蓝海",
                time: "2018-09-20"
              },
              {
                title: "中科院在LED可见光通信技术研发获新突破",
                time: "2018-09-20"
              },
              {
                title: "欧司朗发布全球首款Thread网络协议控制智能LED灯",
                time: "2018-09-20"
              },
              {
                title: "晶科COB家族扩容，再添COB+产品",
                time: "2018-09-20"
              },
              {
                title:
                  "【喜讯】南昌大学硅衬底LED项目荣获国家科学技术发明奖一等奖",
                time: "2018-09-20"
              },
              {
                title:
                  "2018中国（上海）国际人工智能展览会暨OFweek（第二届）人工智能产业大会正",
                time: "2018-09-20"
              },
              {
                title:
                  "维科杯·OFweek 2018中国高科技产业投资机构年度评选获奖名单揭晓",
                time: "2018-09-20"
              }
            ];
            break;
          default:
            data = that.widgetMessage[0].message || [{ title: "传入数据为空" }];
        }
        return data.slice(0, 8);
      }
    }
  },
  methods: {
    //文章列表页的跳转
    routerPath(item) {
      this.query.message = Object.assign({}, item);
      delete this.query.message.message; //删除不必要的数据
      let path = item.path;
      let data = JSON.stringify(this.query); ///利用JSON传输，避免刷新后出现[Object,Object]的情况
      if (path) {
        this.$router.push({ path: path, query: { queryMsg: data } });
      }
    },
    //文章详细页的跳转
    routerLink: function(childrenPath) {
      if (this.query.id) {
        // let path =
        //   this.message[this.currentIndex].path +
        //   this.message[this.currentIndex].path +
        //   "_" +
        //   childrenPath;
        let path = childrenPath;
        delete this.query.message.message; //删除不必要的数据
        let data = JSON.stringify(this.query); ///利用JSON传输，避免刷新后出现[Object,Object]的情况
        this.$router.push({ path: path, query: { queryMsg: data } });
      }
    },
    fresh(val) {
      let that = this;
      if (that.message[val].type) {
        that.type = that.message[val].type;
        if (!that.widgetType)
          that.content = that.message[val].message || [
            { title: "传入数据为空" }
          ];
      }
    }
  },
  watch: {
    currentIndex: {
      //监听标题变更
      handler(val) {
        this.fresh(val);
      },
      immediate: true //在watch中声明后立即执行
    },
    widgetMessage: {
      //深度监听传入数据
      handler(val) {
        if (val) {
          this.message = val;
          this.currentIndex = 0;
          this.content = this.message[0].message || [{ title: "传入数据为空" }];
        }
      },
      deep: true
    }
  },
  created: function() {},
  mounted: function() {},
  components: { Slide }
};
</script>
<style <style lang="scss" scoped>
$blue: #127de7;
.fa-title {
  // margin-top: 2px;
  vertical-align: middle;
  font-size: 1.5em;
  color: #333;
}
.title {
  margin-bottom: 10px;
  margin-top: 2px;
  width: 100%;
  background: #fff;
  height: 36px;
  line-height: 28px;
  box-sizing: border-box;
}
.title li {
  float: left;
}
.belong-title {
  float: left;
  display: inline-block;
  padding: 0 10px;
  margin-right: 40px;
  cursor: default;
}
.belong-title span {
  font-size: 1em;
  font-weight: 500;
  color: #404040;
  line-height: 25px;
  vertical-align: middle;
}
.widget-title a {
  cursor: pointer;
  color: #7b7b7b;
  font-size: 1em;
  font-weight: 500;
  line-height: 25px;
  vertical-align: middle;
  margin-right: 10px;
}
li.more-list {
  & {
    float: right;
  }
  a {
    & {
      font-size: 14px;
      float: right;
      height: 36px;
      line-height: 36px;
      display: inline-block;
      margin-right: 13px;
      padding-left: 20px;
      cursor: pointer;
      color: $blue;
    }
    &:before {
      content: "+";
      display: inline-block;
      color: $blue;
      text-align: center;
      vertical-align: top;
      position: static;
      font-size: 20px;
      width: 20px;
      height: 29px;
      line-height: 33px;
    }
  }
}
</style>



