<template>
  <div class="m-footer">

    <div class="container">
      <nav class="footer_nav">
        <router-link
          to='/home'
          class="footer__link"
        >首页</router-link>
        <span>|</span>
        <router-link
          to='/about'
          class="footer__link"
        >关于我们</router-link>
        <span>|</span>
        <a
          class="footer__link"
          href="javascript:void(0);"
          @click="addFavourite"
        >加入收藏</a>
      </nav>
      <div class="copyright">
        <p>© 版权所有 ******</p>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "m-footer",
  data() {
    return {};
  },
  methods: {
    addFavourite() {
      let that = this;
      let title = document.title;
      let url = document.location.href;
      console.log(window);
      try {
        window.external.AddFavorite(url, title);
      } catch (e) {
        try {
          window.sidebar.addPanel(title, url, "");
        } catch (e) {
          that.$message({
            message:
              "抱歉，您所使用的浏览器无法完成此操作。\n\n请使用快捷键Ctrl+D进行添加！",
            type: "warning"
          });
        }
      }
    }
  }
};
</script>
<style scoped>
.m-footer {
  background: rgb(83, 83, 83);
  border-top: 1px solid rgb(170, 170, 170);
  clear: both;
}
.footer_nav {
  padding-top: 30px;
  padding-bottom: 20px;
  text-align: center;
  color: #cecbcb;
  font-size: 20px;
}
.footer_nav > .footer__link {
  color: #ffffff;
  padding: 10px 20px;
  font-size: 14px;
}
* {
  border: none;
  padding: 0;
  margin: 0;
}
.copyright {
  text-align: center;
  color: #ffffff;
  font-size: 12px;
  padding: 10px 0;
  padding-bottom: 10px;
}
p {
  margin: 0 0 10px;
}
</style>



