<template>
  <div id="search">
    <div id="left">
        <div id="location">
        当前位置：<router-link to="/home" class="selectable">服务平台</router-link> > <span class="blue">搜索</span> : <span class="blue">{{content}}</span>
        </div>
        <div class="whiteBox">
            <ul v-if="list">
                <li v-for="i in list">{{i.title}}</li>
            </ul>
            <div class="center">
                <span v-if="!list||list.length==0" v-bind:class='{blue:tip=="搜索结果获取中"}'>{{tip}}</span>
            </div>
        </div>
    </div>
    <!-- <enterpriseRight></enterpriseRight> -->
  </div>
</template>

<script>
import MHeader from '@/components/common/m-header'
// import enterpriseRight from "@/components/enterprise/enterpriseRight";
export default {
    name: "search",
    components: {
        // enterpriseRight
    },
    data(){
        return{
            list:undefined,
            content:'',
            tip:'搜索结果获取中'
        }
    },
    watch:{
        $route(){
            this.getSearch();
        }
    }
    ,
    methods:{
        getSearch:function(){
            this.list=undefined;
            this.tip="搜索结果获取中";
            this.content=this.$route.params.content;
            this.axios.get(this.HOST + '/common/search',{
                params:{
                    content:this.content
                }
            }).then((res)=>{
                this.list=res.data.message.results.list;
                if (this.list.length==0) this.tip="此关键词没有搜索结果"
                console.log(res.data.message);
            })
        }
    },
    created:function(){
        this.getSearch();
    }
}
</script>
<style lang="scss" scoped>
*{
    padding:0;
    margin:0;
}
div#search{
    width:1200px;
    margin:0 auto;
}
div#left{
    width:710px;
    float:left;
}
div#location{
    background:rgba(255,255,255,0.8);
    margin-top:10px;
    padding:5px;
    width:700px;
}
.blue{
    color:#127de7;
}
.selectable:hover{
    cursor:pointer;
    color:#127de7;
}
div.whiteBox{
    padding:10px;
    padding-bottom:5px;
    width:690px;
    background:white;
    margin-bottom:10px;
}
li{
    margin-bottom:5px;
}
li:hover{
    cursor:pointer;
    color:#127de7;
}
.center{
    text-align:center;
}
</style>



