<template>
  <div id="home">
    <!-- 广告 -->
    <div class="advertise" style="margin-top:10px;margin-bottom:15px;">
      <advertise :messages="messages2"></advertise>
    </div>
    <div class="box1">
      <!-- 资讯 -->
      <div class="box1-left">
        <keep-alive>
          <pb-list :widgetMessage="tabs|FormatDate('资讯')"></pb-list>
        </keep-alive>
      </div>
      <!-- 用户 -->
      <div class="box1-right">
        <keep-alive>
          <user-box></user-box>
        </keep-alive>
      </div>
    </div>
    <div class="box1">
      <!-- 协会 -->
      <div class="box1-left">
        <keep-alive>
          <pb-list :widgetMessage="tabs|FormatDate('协会')"></pb-list>
        </keep-alive>
      </div>
      <!-- 活动 -->
      <div class="box1-right">
        <keep-alive>
          <pb-list :widgetMessage="tabs|FormatDate('活动')" :widgetType="1"></pb-list>
        </keep-alive>
      </div>
    </div>
    <div class="box1">
      <!-- 企业 -->
      <div class="box1-left">
        <keep-alive>
          <pb-list :widgetMessage="tabs|FormatDate('企业')"></pb-list>
        </keep-alive>
      </div>
      <!-- 广告（横滚） -->
      <div class="box1-right">
        <ul class="title">
          <li class="list_title">
            <a style="margin-left:15px;" class="active">广告招收栏目</a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <slide2></slide2>
          </keep-alive>
        </div>
      </div>
    </div>
    <div class="box1">
      <!-- 产品 -->
      <div class="box1-left">
        <keep-alive>
          <pb-list :widgetMessage="tabs|FormatDate('产品')"></pb-list>
        </keep-alive>
      </div>
      <!-- 产品搜索 -->
      <div class="box1-right">
        <ul class="title">
          <li class="list_title">
            <a class="active" style="margin-left:15px;">产品库搜索</a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <pb-search></pb-search>
          </keep-alive>
        </div>
      </div>
    </div>
    <div class="box1">
      <!-- 技术 -->
      <div class="box1-left">
        <keep-alive>
          <pb-list :widgetMessage="tabs|FormatDate('技术')"></pb-list>
        </keep-alive>
      </div>
      <!-- 新闻热点 -->
      <div class="box1-right">
        <keep-alive>
          <pb-list :widgetMessage="tabs|FormatDate('新闻热点')"></pb-list>
        </keep-alive>
      </div>
    </div>
    <div class="box1">
      <!-- 人才 -->
      <div class="box3-left">
        <keep-alive>
          <pb-list :widgetMessage="tabs|FormatDate('人才')"></pb-list>
        </keep-alive>
      </div>
      <!-- 最新招聘 -->
      <div class="box3-right">
        <keep-alive>
          <pb-list :widgetMessage="tabs|FormatDate('招聘')" :widgetType="1"></pb-list>
        </keep-alive>
      </div>
    </div>
  </div>
</template>
<script>
import Advertise from "@/components/common/tools/advertise";
import imgs from "@/common/imgs";
import PbList from "@/components/common/tools/pb-list";
import PbSearch from "@/components/common/tools/pb-search";
import UserBox from "@/components/common/tools/user-box";
import slide2 from "@/components/common/tools/slide2";
import { getIndex, getIndexNewsHotSpot } from "@/api/getData";

export default {
  name: "home",
  data() {
    return {
      messages1: global.advertiseImgs,
      messages2: global.advertiseImgs,
      tabs: global.tabs
    };
  },
  computed: {
    tab1() {
      //资讯内容
      // return this.tabs.
    }
  },
  filters: {
    FormatDate: function(value, type) {
      if (type) {
        value = value.filter(item => {
          if (item.belong == type) return true;
        });
      }
      return value;
    }
  },
  methods: {
    tabLink() {},
    init() {
      let that = this;
      getIndex(this.HOST)
        .then(res => {
          if (res.result == "error") {
            that.$message.error({
              showClose: true,
              message: "获取首页信息异常",
              duration: 2000
            });
          } else {
            global.tabs[0].message = res.message.information1;
            global.tabs[1].message = res.message.hotspot;
            global.tabs[2].message = res.message.policy;
            global.tabs[3].message = res.message.report; //行业报告;
            global.tabs[4].message = res.message.notices;
            global.tabs[5].message = res.message.news;
            global.tabs[6].message = res.message.members;
            global.tabs[7].message = [{ title: "不需要传入数据" }]; //协会之窗;不需要
            global.tabs[8].message = res.message.educationTrain; //教育培训;
            global.tabs[9].message = res.message.companyNews;
            global.tabs[10].message = res.message.companyBrand;
            global.tabs[11].message = res.message.companyDynamics;
            global.tabs[12].message = res.message.productRecommend;
            global.tabs[13].message = res.message.productNews;
            global.tabs[14].message = res.message.productEvaluation;
            global.tabs[15].message = res.message.productLibrary;
            global.tabs[16].message = res.message.science;
            global.tabs[17].message = res.message.basic;
            // global.tabs[18].message = res.message.case//案例库;不可用
            global.tabs[19].message = res.message.experts;
            global.tabs[20].message = res.message.expertArts;
            global.tabs[21].message = res.message.school;
            global.tabs[25].message = res.message.meeting; //活动
            global.tabs[26].message = res.message.conference;
            global.tabs[33].message = res.message.meeting; //会议
            global.tabs[34].message = res.message.conference;//展会

            global.tabs[27].message = res.message.other;
            global.tabs[31].message = res.message.hotspot; //相关热点
            global.tabs[32].message = res.message.position.map(item => {
              item.title = item.positionName;
              return item;
            }); //最新招聘
          }
        })
        .catch(function(response) {
          that.$message.error({
            showClose: true,
            message: "主页数据异常",
            duration: 2000
          });
        });
      getIndexNewsHotSpot(this.HOST)
        .then(res => {
          if (res.result == "success") {
            global.tabs[28].message = res.message.newsHotSpotDay;
            global.tabs[29].message = res.message.newsHotSpotMonth;
            global.tabs[30].message = res.message.newsHotSpotWeek;
          } else if (res.result == "error") {
            that.$message.error({
              showClose: true,
              message: "主页数据异常",
              duration: 2000
            });
          }
        })
        .catch(function(response) {
          that.$message.error({
            showClose: true,
            message: "主页数据异常",
            duration: 2000
          });
        });
    }
  },
  mounted() {
    this.init();
  },
  activated() {
    global.currentPath.data = [1];
  },
  components: {
    Advertise,
    PbList,
    PbSearch,
    UserBox,
    slide2
  }
};
</script>

<style lang="scss" scoped>
@mixin adv {
  margin: 5px auto;
  width: 1200px;
  height: 125px;
  overflow: hidden;
}
@mixin w-box {
  margin: 5px auto;
  width: 1200px;
  height: 312px;
  overflow: hidden;
  margin-bottom: 18px;
}
@mixin i-box {
  box-shadow: 0 0 1px #bebebe;
  height: 100%;
  overflow: hidden;
  box-sizing: border-box;
  border-radius: 2px;
}
@mixin border {
  -webkit-box-shadow: 0 0 1px #bebebe;
  box-shadow: 0 0 1px #bebebe;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border: 1px solid rgba(204, 204, 204, 0.377);
}
.active {
  color: #127de7 !important;
  border-bottom: 2px solid #127de7;
  padding-bottom: 4px;
}
.advertise {
  @include adv;
  margin-top: 5px;
  margin-bottom: 20px;
}
.box1 {
  @include w-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.box1-left {
  background: rgba(255, 255, 255, 0.95);
  width: 795px;
  float: left;
  @include i-box;
  @include border;
}
.box1-right {
  background: rgba(255, 255, 255, 0.95);
  @include i-box;
  @include border;
  width: 390px;
  float: right;
}
.box2-left {
  background: rgba(255, 255, 255, 0.95);
  @include i-box;
  @include border;
  width: 406px;
  float: left;
}
.box2-right {
  @include i-box;
  background: rgba(255, 255, 255, 0.95);
  @include border;
  width: 375px;
  float: left;
  margin-left: 15px;
}
.box3-left {
  background: rgba(255, 255, 255, 0.95);
  @include i-box;
  width: 390px;
  float: left;
  @include border;
}
.box3-right {
  background: rgba(255, 255, 255, 0.95);
  width: 795px;
  float: right;
  @include border;
  @include i-box;
}
.title {
  margin-bottom: 10px;
  margin-top: 2px;
  width: 100%;
  background: #fff;
  height: 32px;
  line-height: 28px;
}
.title li {
  float: left;
}
</style>



