<!--
 * @Description: 专家库、高校列表
 -->
<template>
  <div id="main-list-talent">
    <!-- 加载框 -->
    <div v-if="loading">
      <h1 id="bigTitle">列表正在加载... loading...</h1>
      <noContent :loadName="'列表'"></noContent>
    </div>
    <!-- 存放列表数据的框 -->
    <div v-show="!loading" id="list-box">
      <!-- 列表 -->
      <div class="lists">
        <li v-for="(item,index) in currentList" :key="index">
          <div class="right_part">
            <div class="title_hover">
              <!-- <i class="fa fa-flag fa-2x"></i> -->
              <h2 class="title" @click="routerLink(currentList[index].id)">
                <a>
                  {{item.name}}
                </a>
              </h2>
            </div>
            <div class="summary">
              <p class="pre_view">
                {{item.introduction.slice(0,200)}}...
              </p>
            </div>
          </div>
        </li>
      </div>
      <!-- 分页组件 -->
      <div class="pagination">
        <el-pagination @current-change="handleCurrentChange" background :current-page.sync="currentPage" :page-size="pageSize" layout="prev, pager, next, jumper" :total="total">
        </el-pagination>
      </div>
    </div>

  </div>
</template>
<script>
import { getContentList } from "@/api/getData";
import noContent from "@/components/index/content/no-content";

export default {
  props: ["queryProp"],
  name: "main-list-talent",
  data() {
    return {
      queryMsg: "",
      currentList: "",
      total: 0,
      pageSize: 12,
      currentPage: 1,
      hostUrl: "",
      loading: true
    };
  },
  methods: {
    routerLink: function(id) {
      let query = new Object();
      query.id = id;
      query.message = this.queryMsg;
      let path = 'content';
      delete query.message.message; //删除不必要的数据
      let data = JSON.stringify(query); ///利用JSON传输，避免刷新后出现[Object,Object]的情况
      this.$router.push({ path: path, query: { queryMsg: data } });
    },
    //获取列表
    getData() {
      let that = this;
      let url = this.hostUrl;
      let pageNum = that.currentPage;
      that.loading = true;
      getContentList(url, pageNum)
        .then(res => {
          let data = res.message;
          if (data) {
            that.loading = false;
            that.currentList = data.list;
            that.total = data.total;
            that.currentPage = data.pageNum;
            that.pageSize = data.pageSize;
          }
        })
        .catch(err => {
          that.loading = false;
          that.$message.error({
            showClose: true,
            message: "获取文章列表错误",
            duration: 2000
          });
        });
    },
    //更改页数
    handleCurrentChange(val) {
      this.currentPage = val;
      this.getData();
    }
  },
  watch: {
    queryProp: {
      handler(val) {
        if (val) {
          this.queryMsg = this.queryProp.message;

          if (this.queryMsg.requestPath) {
            this.hostUrl = this.HOST + this.queryMsg.requestPath; //拼接请求路径
            this.getData(); //调用获取文章列表方法
          }
        }
      },
      immediate: true,
      deep: true
    }
  },
  components: { noContent }
};
</script>

<style lang="scss" scoped>
$blue: #127de7;
.title:hover > a {
  color: $blue;
}
#main-list-talent {
  margin-top: 16px;
  background: #f6f6f6;
  border-radius: 2px;
}
h1#bigTitle {
  text-align: center;
  padding: 30px;
}
.list {
  & {
    background: #f6f6f6;
    padding: 5px 10px;
    box-sizing: border-box;
  }
  .title_hover {
    padding-left: 5px;
  }
}
.right_part {
  box-sizing: border-box;
  border-bottom: dashed 1px #ccc;
  -webkit-box-shadow: 0 0 1px #bebebe;
  box-shadow: 0 0 1px #bebebe;
  width: 100%;
  height: 120px;
  display: inline-block;
  padding: 5px 10px;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.pre_view {
  white-space: nowrap;
  text-overflow: ellipsis;
  width: 700px;
  overflow: hidden;
}
#x_cahrtlist_single {
  padding: 3px;
}
i {
  vertical-align: top;
  margin-top: 12px;
  color: $blue;
}
i,

h2 {
  font-size: 18px;
  font-weight: bold;
  line-height: 24px;
  height: 24px;
  margin-bottom: 4px;
  display: inline-block;
  padding-left: 10px;
}
h2 a {
  color: #3d3d3d;
  display: block;
  line-height: 24px;
  height: 24px;
  word-wrap: break-word;
  word-break: break-all;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 680px;
  &:hover {

    cursor: pointer;
  }
}
.summary {
  padding-left: 10px;
  margin-bottom: 4px;
  color: #8a8a8a;
  font-size: 14px;
  line-height: 24px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  height: 50px;
}
.right_part_bottom {
  height: auto;
}
.right_part_bottom div {
  display: inline-block;
  font-size: 14px;
  color: #8a8a8a;
  line-height: 24px;
  word-break: break-all;
  padding: 0 5px 0 5px;
}
.sort_type,
time {
  float: left;
}
.title {
  //只有设在最近的才有用
  width: 730px;
  font-size: 20px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  font-family: "\5FAE\8F6F\96C5\9ED1", "Microsoft Yahei", Verdana, sans-serif;
  margin-top: 20px;
}

.time {
  padding: 5px 0 5px 0;
  position: relative !important;
  div {
    display: inline-block;
    font-size: 14px;
    color: #8a8a8a;
    line-height: 24px;
    word-break: break-all;
  }
}

.pagination {
  padding: 20px 0;
}
</style>



