<template>
  <div class="m-header">
    <!-- 标题导航搜索 -->
    <div class="m-header2">
      <div class="headering">
        <div class="title">
          <h1 @click="routerLink('/')">智能装备</h1>
          <h2>公众服务平台</h2>
        </div>
        <div class="right-side">
          <!-- 导航栏搜索框 -->
          <div class="search">
            <li class="sw1">
              <div class="spw1">
                <label>
                  <input type="text" id="searching1" v-model="content" placeholder="请输入关键字">
                </label>
                <select v-model='channel'>
                  <option v-for="(item,index) in choices" :key="index">{{item.name}}</option>
                </select>
              </div>
            </li>
            <li class="sw2">
              <input type="submit" value="搜索" @click.stop="routerLinkName('search',content,'content')">
            </li>
          </div>
          <div id="m">M</div>
          <div class="m">商城</div>
          <div class="m">需求发布</div>
        </div>
      </div>
      <!-- nav -->
      <div class="menu">
        <ul class="menu_list">
          <li v-for="(menu,index) in $router.options.routes[0].children" v-if="menu.menuShow" :key="index" @click="routerLink(index,menu.path)" :class="menuIndex === index ? 'active' :''">
            <i v-bind:class="menu.icon" class="fa-2x fa" aria-hidden="true"></i>
            <span>{{menu.showName}}</span>
          </li>
        </ul>
      </div>
    </div>

  </div>
</template>

<script>
import nav from "@/common/nav";
import eventBus from "@/common/eventBus";

export default {
  name: "m-header",
  data() {
    return {
      content: "", //搜索框内容
      channel: global.choices[0].name, //搜索框选项
      choices: global.choices,
      menuIndex: global.currentPath.data[0]
    };
  },
  created() {
    let that = this;
    Object.defineProperty(global.currentPath, "data", {
      get() {
        return this._data;
      },
      set(val) {
        if (Array.isArray(val)) {
          if (global.currentPath.path == "政策") {
            //判断是政策则特殊对待
            global.currentPath.path ='' //作用一次后清除，防止影响后续
            val = [3];
          } else {
            this._data = val;
          }
          that.menuIndex = val[0]; //模拟监听，当global.currentPath.data被调用set时更改导航栏高亮
          eventBus.$emit("SynchronizePath", val); //同步到导航数据
          return val;
        }
      }
    });
  },
  methods: {
    //路由跳转
    routerLink(index, path) {
      this.$router.push(path);
      global.currentPath.data = [index]; //给menuIndex赋值点击的路由下标
    },
    //检索当前路径，路由跳转
    routerLinkName: function(
      theName,
      paramValue,
      paramName,
      param2Value,
      param2Name
    ) {
      this.params = this.params || new Object();
      this.params[paramName] = paramValue;
      if (param2Name) {
        this.params[param2Name] = param2Value;
      }
      this.$router.push({ name: theName, params: this.params });
    }
  }
};
</script>

<style lang="scss" scoped>
// 清除浮动伪类
@mixin after {
  display: block;
  content: ".";
  clear: both;
  line-height: 0;
  visibility: hidden;
}
//全局默认的蓝色
$blue: #127de7;
.none {
  display: none;
}
.background {
  background: url(../../assets/seach-bot.jpg) no-repeat 0;
}
.fa-2x {
  font-size: 2em;
  color: inherit;
}
.m-header {
  select {
    width: 63px;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    border: 0;
    background: #fff;
    text-align: center;
    border-left: solid 1px rgb(169, 169, 169);
  }
  .m-header2 {
    & {
      border-radius: 2px;
      margin: 0 auto;
      width: 1200px;
      background: rgba(255, 255, 255, 0.95);
    }
    .headering {
      & {
        zoom: 1;
        margin-bottom: 20px;
        margin-top: 15px;
      }
      &:after {
        @include after;
      }
      .title {
        & {
          float: left;
          margin: 10px;
        }
        &::after {
          content: " ";
          display: block;
          width: 100%;
          height: 2px;
          margin-top: 10px;
          background: -moz-linear-gradient(
            left,
            rgba(167, 22, 196, 0) 0%,
            rgba(22, 196, 173, 0.8) 20%,
            rgb(64, 101, 203) 53%,
            rgba(22, 196, 153, 0.8) 79%,
            rgba(103, 22, 196, 0) 100%
          );
          background: -webkit-gradient(
            linear,
            left top,
            right top,
            rgba(167, 22, 196, 0) 0%,
            rgba(22, 196, 173, 0.8) 20%,
            rgb(64, 101, 203) 53%,
            rgba(22, 196, 153, 0.8) 79%,
            rgba(103, 22, 196, 0) 100%
          );
          background: -webkit-linear-gradient(
            left,
            rgba(167, 22, 196, 0) 0%,
            rgba(22, 196, 173, 0.8) 20%,
            rgb(64, 101, 203) 53%,
            rgba(22, 196, 153, 0.8) 79%,
            rgba(103, 22, 196, 0) 100%
          );
          background: -o-linear-gradient(
            left,
            rgba(167, 22, 196, 0) 0%,
            rgba(22, 196, 173, 0.8) 20%,
            rgb(64, 101, 203) 53%,
            rgba(22, 196, 153, 0.8) 79%,
            rgba(103, 22, 196, 0) 100%
          );
          background: -ms-linear-gradient(
            left,
            rgba(167, 22, 196, 0) 0%,
            rgba(22, 196, 173, 0.8) 20%,
            rgb(64, 101, 203) 53%,
            rgba(22, 196, 153, 0.8) 79%,
            rgba(103, 22, 196, 0) 100%
          );
          background: linear-gradient(
            left,
            rgba(167, 22, 196, 0) 0%,
            rgba(22, 196, 173, 0.8) 20%,
            rgb(64, 101, 203) 53%,
            rgba(22, 196, 153, 0.8) 79%,
            rgba(103, 22, 196, 0) 100%
          );
        }
      }
      h1 {
        color: $blue;
        cursor: pointer;
        user-select: none;
        background: -webkit-repeating-linear-gradient(
          -45deg,
          rgb(26, 20, 75),
          rgb(10, 10, 71) 20px,
          rgb(0, 101, 141) 20px,
          rgb(13, 3, 124) 40px,
          rgb(36, 12, 170) 40px
        );
        -webkit-text-fill-color: transparent;
        -webkit-background-clip: text;
        display: inline-block;
        margin: 15px 75px 0 65px;
        font-weight: 500;
        text-align: left;
        font-size: 2.1em;
      }
      h2 {
        color: #888;
        margin: 4px 75px 5px 65px;
        text-align: left;
        font-weight: 500;
        font-size: 1.05em;
        letter-spacing: 0.3em;
      }
      .right-side {
        zoom: 1;
        float: left;
      }
      .search::after {
        @include after;
      }
      .search {
        & {
          float: left;
          width: 506px;
          height: 80px;
          margin: 40px 25px 0 40px;
        }
        li.sw1 {
          width: 386px;
          height: 35px;
          border: 4px solid $blue;
          border-right: none;
          display: block;
          position: relative;
          float: left;
          border-top-left-radius: 20px;
          border-bottom-left-radius: 20px;
        }
        li.sw1 label {
          float: left;
          margin: 0;
          padding: 0;
        }
        .spw1 {
          width: 386px;
          height: 30px;
        }
        .sw1 input {
          width: 323px;
          height: 35px;
          border: 1px solid #e6e6e6;
          border-right: none;
          box-sizing: border-box;
          display: block;
          padding-left: 15px;
          border-top-left-radius: 20px;
          border-bottom-left-radius: 20px;
          outline: none;
          border: none;
          -webkit-box-shadow: inset 0px 0px 0px 30px #fff;
          -moz-box-shadow: inset 0px 0px 0px 30px #fff;
          box-shadow: inset 0px 0px 0px 30px #fff;
        }
        .down_menu {
          width: 60px;
          height: auto;
          position: relative;
          z-index: 5;
          cursor: pointer;
        }
        .down_menu span {
          width: 60px;
          height: 35px;
          line-height: 29px;
          text-align: center;
          display: block;
          border: none;
          border-left: 1px solid #dcdcdc;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        li.sw2 {
          width: 100px;
          height: 43px;
          line-height: 40px;
          background: $blue;
          text-align: center;
          display: block;
          float: left;
          border-top-right-radius: 20px;
          border-bottom-right-radius: 20px;
          cursor: pointer;
        }
        .sw2 input {
          border: none;
          background: $blue;
          cursor: pointer;
          color: #fff;
          font: 20px "微软雅黑";
          box-sizing: border-box;
          vertical-align: middle;
        }
        .down_menu li {
          display: block;
          float: left;
          cursor: pointer;
          width: 60px;
          height: 29px;
          background: #fff;
          line-height: 29px;
          text-align: center;
          position: relative;
          z-index: 5;
          left: 0;
          border: 1px solid #c7c7c7;
          border-top: none;
          border-bottom: none;
          font-size: 12px;
          user-select: none;
        }
        .down_menu li:hover {
          background: $blue;
          color: #fff;
        }
      }
      #m {
        font-style: italic;
        float: left;
        font-size: 38px;
        color: $blue;
        margin: 32px 10px 0 10px;
        user-select: none;
      }
      .m {
        float: left;
        font-size: 20px;
        color: #a3a3a3;
        font-weight: 500;
        margin: 45px 10px 0 40px;
      }
    }
    .menu {
      & {
        zoom: 1;
      }
      &::after {
        content: " ";
        display: block;
        width: 100%;
        height: 0.5px;
        margin-top: 10px;
        background: -moz-linear-gradient(
          left,
          rgba(196, 22, 22, 0) 0%,
          rgba(196, 22, 22, 0.8) 20%,
          rgb(64, 101, 203) 53%,
          rgba(196, 22, 22, 0.8) 79%,
          rgba(196, 22, 22, 0) 100%
        );
        background: -webkit-gradient(
          linear,
          left top,
          right top,
          rgba(22, 176, 196, 0) 0%,
          rgba(22, 196, 173, 0.8) 20%,
          rgb(64, 101, 203) 53%,
          rgba(22, 196, 153, 0.8) 79%,
          rgba(22, 196, 153, 0) 100%
        );
        background: -webkit-linear-gradient(
          left,
          rgba(196, 22, 22, 0) 0%,
          rgba(196, 22, 22, 0.8) 20%,
          rgb(64, 101, 203) 53%,
          rgba(196, 22, 22, 0.8) 79%,
          rgba(196, 22, 22, 0) 100%
        );
        background: -o-linear-gradient(
          left,
          rgba(196, 22, 22, 0) 0%,
          rgba(196, 22, 22, 0.8) 20%,
          rgb(64, 101, 203) 53%,
          rgba(196, 22, 22, 0.8) 79%,
          rgba(196, 22, 22, 0) 100%
        );
        background: -ms-linear-gradient(
          left,
          rgba(196, 22, 22, 0) 0%,
          rgba(196, 22, 22, 0.8) 20%,
          rgb(64, 101, 203) 53%,
          rgba(196, 22, 22, 0.8) 79%,
          rgba(196, 22, 22, 0) 100%
        );
        background: linear-gradient(
          left,
          rgba(196, 22, 22, 0) 0%,
          rgba(196, 22, 22, 0.8) 20%,
          rgb(64, 101, 203) 53%,
          rgba(196, 22, 22, 0.8) 79%,
          rgba(196, 22, 22, 0) 100%
        );
      }
      .menu_list {
        & {
          display: block;
          margin: 0 auto;
          width: 100%;
          box-sizing: border-box;
          height: auto;
          margin-top: 1px;
          -webkit-padding-start: 0px;
          padding: 3px 0;
        }
        &:after {
          @include after;
        }
        li {
          & {
            user-select: none;
            cursor: pointer;
            width: 84px;
            height: auto;
            line-height: 1.6;
            float: left;
            display: block;
            text-align: center;
            margin-right: 1px;
            color: rgb(51, 51, 51);
          }
          &:hover i,
          &:hover {
            color: $blue;
          }
          span {
            display: block;
            font-size: 16px;
            letter-spacing: 1px;
            font-weight: 600;
          }
        }
      }
    }
  }
}
</style>



