<template>
  <div id="contain">
    <div class="bg-img">
      <div class="bg-content">
        <!-- 页眉 -->
        <div class="m-header1">
          <!-- <div class="media">
            <span class="ex1" @click="selectItem()">登录</span>
            <span class="vertical_line"></span>
            <span class="ex1" @click="selectItem('register')">注册</span>
          </div> -->
          <div class="media2" v-if="rank>0&&  rank<5">
            <span class="vertical_line"></span>
            <a class="ex2" href="admin.html" target="_blank">后台管理系统</a>
          </div>
          <div class="topbar-account topbar-btn media">
            <el-dropdown trigger="click">
              <span class="el-dropdown-link userinfo-inner"><i class="fa fa-user" aria-hidden="true"></i> {{rank!=0?username:'未登录'}} <i class="el-icon-caret-bottom"></i></span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item>
                  <div @click="routerLink(1)"><span style="color: #555;font-size: 14px;">{{rank!=0?'个人中心':'个人注册'}}</span></div>
                </el-dropdown-item>
                <el-dropdown-item>
                  <div @click="routerLink(2)"><span style="color: #555;font-size: 14px;" v-if="rank!=5&&rank!=4">会员注册</span></div>
                </el-dropdown-item>
                <el-dropdown-item>
                  <div @click="routerLink(3)"><span style="color: #555;font-size: 14px;">会员中心</span></div>
                </el-dropdown-item>
                <el-dropdown-item>
                  <div @click="routerLink(4)"><span style="color: #555;font-size: 14px;">{{rank!=0?'修改密码':'忘记密码'}}</span></div>
                </el-dropdown-item>
                <el-dropdown-item divided @click.native="logout()" v-if="rank">退出登录</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </div>
        </div>
        <!-- 首部 -->
        <keep-alive>
          <m-header></m-header>
        </keep-alive>
        <!-- 内容tabs区 -->
        <keep-alive>
          <router-view class="animated slideInLeft"></router-view>
          <!-- <router-view></router-view> -->
        </keep-alive>
        <!-- 尾部 -->
        <m-footer></m-footer>
        <!-- 加载动画 -->
        <loading-first v-show="loading"></loading-first>
      </div>
    </div>

  </div>
</template>
<script>
import MFooter from "@/components/common/m-footer";
import MHeader from "@/components/common/m-header";
import LoadingFirst from "@/components/common/tools/loading1";
import eventBus from "@/common/eventBus";
import { getLoginData } from "@/api/sign.js";

export default {
  name: "contain",
  components: {
    MFooter,
    MHeader,
    LoadingFirst
  },
  data() {
    return {
      loading: false,
      rank: global.userItem.rank,
      username: ""
    };
  },
  methods: {
    routerLink(i) {
      let path; //跳转的路径
      let query = {}; //传递的数据
      let that = this;
      switch (i) {
        case 1:
          path = "/user_1";
          if (that.rank) path = "/userCenter";
          //个人中心
          else {
            query = 1;
          } //注册
          that.$router.push({ path: path, query: { type: query } });
          break;
        case 2:
          path = "/member_1";
          that.$router.push({ path: path, query: { type: query } });
          break;
        case 3:
          path = "/memberCenter";
          that.$router.push({ path: path, query: { type: query } });
          break;
        case 4:
          path = "/user_1";
          if (that.rank) query = 2;
          //修改密码
          else {
            query = 2;
          } //忘记密码
          that.$router.push({ path: path, query: { type: query } });
          break;
        default:
          break;
      }
    },
    checkRouterLocal() {
      let path = this.$route.path;
      //查找当前路由
      global.currentPath.data[0] = this.$router.options.routes[0].children.findIndex(
        menu =>
          (menu.path === path || path.indexOf(menu.path) != -1) &&
          menu.path != ""
      );
    },
    checkLoginStatus() {
      let that = this;
      //查看当前登录状态
      getLoginData(this.HOST).then(res => {
        global.userItem.rank = res.message;
        that.rank = res.message;
        global.userItem.userName = localStorage.getItem("robotKeyName");
        eventBus.$emit("SynchronizeRank", that.rank);
      });
    },
    logout() {
      let that = this;
      eventBus.$emit("SynchronizeLogout");
      setTimeout(function() {
        if (global.userItem.rank) {
          that.axios
            .get(that.HOST + "/user/loginOut")
            .then(response => {
              global.userItem.rank = 0;
              that.rank = 0;
            })
            .catch(() => {});
        }
      }, 200);
    }
  },
  created() {
    let that = this;
    Object.defineProperty(global.userItem, "rank", {
      get() {
        return this._rank;
      },
      set(val) {
        that.rank = val;
        if (val) {
          that.username = localStorage.getItem("robotKeyName");
        }
        this._rank = val;
      }
    });
    that.checkRouterLocal();
    //初始化，查看当前登录状态
    that.checkLoginStatus();
  },
  mounted() {}
};
</script>
<style lang="scss" scoped>
// loading
.m-header1 {
  & {
    min-width: 1200px;
    background-color: hsla(0, 0%, 100%, 0.1);
    color: white;
    padding: 2px 120px;
    box-sizing: border-box;
    width: 100%;
    white-space: nowrap;
    line-height: 1.3;
    zoom: 1;
  }
  &:after {
    display: block;
    content: ".";
    clear: both;
    line-height: 0;
    visibility: hidden;
  }
  .compony_title {
    & {
      -webkit-margin-before: 0.5em;
      -webkit-margin-after: 0.5em;
      -webkit-margin-start: 0px;
      -webkit-margin-end: 0px;
      padding: 0;
      float: left;
      z-index: 6;
      font-weight: 300;
      position: relative;
      letter-spacing: 2px;
      font-size: 23px;
      line-height: 28px;
      font-family: "futura-pt", "Helvetica Neue", Helvetica;
    }
    a {
      & {
        top: 3px;
        left: 0;
        display: block;
        height: 23px;
        color: #dadada;
      }
      span {
        color: #409eff;
        font-weight: 700;
      }
    }
  }
  .icon {
    margin: 0.24em 0;
    float: left;
    overflow: hidden;
    font-size: 16px;
  }
  .media {
    float: right;
    text-align: right;
    overflow: hidden;
    margin: 0.57em 0 0.57em 0;
    font-size: 18px;
    line-height: 1.6;
  }
  .media2 {
    @extend .media;
    float: left;
    color: #e7e7e7;
  }
  .ex1 {
    margin-right: 8px;
    cursor: pointer;
  }
  .ex2 {
    @extend .ex1;
    display: inline-block;
  }
  /*竖直分割线*/
  .vertical_line {
    padding: 0 6px;
    border-left: 1px white solid;
    vertical-align: middle;
  }
}
// 登录状态
.topbar-btn {
  color: #fff;
}
.topbar-account {
  float: right;
  padding-right: 12px;
}
.userinfo-inner {
  cursor: pointer;
  font-size: 16px;
  color: #fff;
  padding-left: 10px;
  line-height: 26px;
}
/*动画的默认设置*/

.animated {
    animation-duration: 300ms!important;
}

.infinite {
    animation-iteration-count: infinite;
    animation-duration: 3210ms!important;
}
</style>



