//router.index.js
import Vue from 'vue'
import Router from 'vue-router'
import contain from '@/components/index/contain'
import home from '@/components/index/tabs/home'
import News from '@/components/index/tabs/news'
import Policy from '@/components/index/tabs/policy'
import Association from '@/components/index/tabs/association'
import Enterprise from '@/components/index/tabs/enterprise'
import Product from '@/components/index/tabs/product'
import Tech from '@/components/index/tabs/tech'
import Talent from '@/components/index/tabs/talent'
import Exhibition from '@/components/index/tabs/exhibition'
import Meeting from '@/components/index/tabs/meeting'
import Public from '@/components/index/tabs/public'
import About from '@/components/index/tabs/about'
import Io from '@/components/index/tabs/io'
import Mall from '@/components/index/tabs/mall'
import search from '@/components/index/search'
import Content from '@/components/index/content/content'
import ContentList from '@/components/index/content/Content-list'


Vue.use(Router)

let router = new Router({
  routes: [
    {
      path: '',
      component: contain,
      children: [
        {
          path: '',
          redirect: home,
        },
        //首页
        {
          showName: "首页",
          path: "/home",
          icon: "fa-home",
          name: 'home',
          menuShow: true,
          component: home,
        },
        //资讯
        {
          showName: "资讯",
          path: "/news",
          icon: "fa-commenting",
          menuShow: true,
          component: News,
          children: [
            {
              path: '',
              name: 'news_0',
              component: () => import('@/components/index/tabs/news-default'),
            },//主页
            {
              path: '/news_1',
              name: 'news_1',
              component: ContentList,
              showSecondName: '行业动态'
            },//行业动态
            {
              path: '/news_2',
              name: 'news_2',
              component: ContentList,
              showSecondName: '资讯热点'
            },//资讯热点
            {
              path: '/news_3',
              name: 'news_3',
              component: ContentList,
              redirect: { name: 'policy' },
              showSecondName: '政策'
            },//政策
            {
              path: '/news_4',
              name: 'news_4',
              component: ContentList,
              showSecondName: '行业报告'
            },//行业报告
            {
              path: '/news_content',
              name: 'news_content',
              component: Content,
            },
          ]
          // alias:'/news',
        },
        //政策
        {
          showName: "政策",
          path: "/policy",
          icon: "fa-file-text",
          name: 'policy',
          menuShow: true,
          component: Policy,
          children: [
            {
              path: '/policy_content',
              name: 'policy_content',
              component: Content,
            },
          ]
          // alias:'/policy',
        },
        //协会
        {
          showName: "协会",
          path: "/association",
          icon: "fa-users",
          menuShow: true,
          component: Association,
          children: [
            {
              path: '',
              name: 'association_0',
              component: () => import('@/components/index/tabs/association-default'),
            },//主页
            {
              path: '/association_1',
              name: 'association_1',
              component: ContentList,
              showSecondName: '通知公告'
            },//通知公告
            {
              path: '/association_2',
              name: 'association_2',
              component: ContentList,
              showSecondName: '协会动态'
            },//协会动态
            {
              path: '/association_3',
              name: 'association_3',
              component: ContentList,
              showSecondName: '协会会员'
            },//协会会员
            {
              path: '/association_4',
              name: 'association_4',
              component: ContentList,
              showSecondName: '教育培训'
            },//教育培训
          ]
          // alias:'/association',
        },
        //企业
        {
          showName: "企业",
          path: "/enterprise",
          icon: "fa-globe",
          menuShow: true,
          component: Enterprise,
          children: [
            {
              path: '',
              name: 'enterprise_0',
              component: () => import('@/components/index/tabs/enterprise-default'),
            },//主页
            {
              path: '/enterprise_1',
              name: 'enterprise_1',
              component: ContentList,
              showSecondName: '企业新闻'
            },//企业新闻
            {
              path: '/enterprise_2',
              name: 'enterprise_2',
              component: ContentList,
              showSecondName: '品牌展厅'
            },//品牌展厅
            {
              path: '/enterprise_3',
              name: 'enterprise_3',
              component: ContentList,
              showSecondName: '会员动态'
            },//会员动态
            {
              path: '/enterprise_4',
              name: 'enterprise_4',
              component: ContentList,
              showSecondName: '会员列表'
            },//会员动态
          ]
          // alias:'/enterprise',
        },
        //产品
        {
          showName: "产品",
          path: "/product",
          icon: "fa-android",
          menuShow: true,
          component: Product,
          children: [
            {
              path: '',
              name: 'product_0',
              component: () => import('@/components/index/tabs/product-default'),
            },//主页
            {
              path: '/product_1',
              name: 'product_1',
              component: ContentList,
              showSecondName: '产品推荐'
            },//产品推荐
            {
              path: '/product_2',
              name: 'product_2',
              component: ContentList,
              showSecondName: '产品新闻'
            },//产品新闻
            {
              path: '/product_3',
              name: 'product_3',
              component: ContentList,
              showSecondName: '产品评测'
            },//产品评测
            {
              path: '/product_4',
              name: 'product_4',
              component: ContentList,
              showSecondName: '产品库'
            },//产品库
          ]
        },
        //技术
        {
          showName: "技术",
          path: "/tech",
          icon: "fa-lightbulb-o",
          menuShow: true,
          component: Tech,
          children: [
            {
              path: '',
              name: 'tech_0',
              component: () => import('@/components/index/tabs/tech-default'),
            },//主页
            {
              path: '/tech_1',
              name: 'tech_1',
              component: ContentList,
              showSecondName: '技术研讨'
            },//技术研讨
            {
              path: '/tech_2',
              name: 'tech_2',
              component: ContentList,
              showSecondName: '基础知识'
            },//基础知识
            {
              path: '/tech_3',
              name: 'tech_3',
              component: ContentList,
              showSecondName: '案例库'
            },//案例库
          ]
        },
        //供需
        {
          showName: "供需",
          path: "/io",
          icon: "fa-tachometer",
          name: 'io',
          menuShow: true,
          component: Io,
        },
        //商城
        {
          showName: "商城",
          path: "/mall",
          icon: "fa-shopping-cart",
          name: 'mall',
          menuShow: true,
          component: Mall,
        },
        //人才
        {
          showName: "人才",
          path: "/talent",
          icon: "fa-id-badge",
          menuShow: true,
          component: Talent,
          children: [
            {
              path: '',
              name: 'talent_0',
              component: () => import('@/components/index/tabs/talent-default'),
            },//主页
            {
              path: '/talent_1',
              name: 'talent_1',
              component: ContentList,
              showSecondName: '专家库'
            },//专家库
            {
              path: '/talent_2',
              name: 'talent_2',
              component: ContentList,
              showSecondName: '高校'
            },//高校
            {
              path: '/talent_3',
              name: 'talent_3',
              component: ContentList,
              showSecondName: '专家智点'
            },//专家智点
          ]
        },
        //展会
        {
          showName: "展会",
          path: "/exhibition",
          icon: "fa-tasks",
          menuShow: true,
          component: Exhibition,
          children: [
            {
              path: '',
              name: 'exhibition_0',
              component: () => import('@/components/index/tabs/exhibition-default')
            },//主页
            {
              path: '/exhibition_1',
              name: 'exhibition_1',
              component: ContentList,
              showSecondName: '展会动态'
            },//展会动态
          ]
        },
        //会议
        {
          showName: "会议",
          path: "/meeting",
          icon: "fa-pencil-square-o",
          menuShow: true,
          component: Meeting,
          children: [
            {
              path: '',
              name: 'meeeting_0',
              component: () => import('@/components/index/tabs/meeting-default')
            },//主页
            {
              path: '/meeting_1',
              name: 'meeting_1',
              component: ContentList,
              showSecondName: '会议动态'
            },//会议动态
          ]
        },
        {//搜索结果
          path: '/search:content',
          name: 'search',
          component: search,
        }
        ,
        //公众号
        {
          showName: "公众号",
          path: "/public",
          icon: "fa-weixin ",
          name: 'public',
          menuShow: true,
          component: Public,
        },
        //关于我们
        {
          showName: "关于我们",
          path: "/about",
          icon: "fa-address-book",
          name: 'about',
          menuShow: true,
          component: About,
        },
        //文章详情页
        {
          path: '/content',
          name: 'content',
          component: Content,
        },
        //招聘
        {
          showName: "招聘",
          path: "/recruitment",
          icon: "fa-id-badge",
          component: () => import('@/components/index/tabs/recruitment'),
          children: [
            {
              path: '',
              name: 'recruitment_0',
              component: () => import('@/components/index/tabs/recruitment-default'),
              showSecondName: '招聘列表'
            },//主页
            {
              path: '/recruitment_1',
              name: 'recruitment_1',
              component: () => import('@/components/index/content/recruitment-content'),
              showSecondName: '招聘详情'
            },//招聘详情页
          ]
        },
        //会员中心
        {
          showName: "会员中心",
          path: '/memberCenter',
          name: '',
          component: () => import('@/components/userCenter/member-center'),
          children: [
            //会员中心：协会=》会员审批；会员：普通会员管理
            {
              showSecondName: '会员列表',
              path: '',
              name: 'member_0',
              component: () => import('@/components/userCenter/member-default')
            },
            //会员注册
            {
              showSecondName: '会员注册',
              path: '/member_1',
              name: 'member_1',
              component: () => import('@/components/userCenter/register/member-register')
            },
            {
              showSecondName: '会员信息',
              path: '/member_2',
              name: 'member_2',
              component: () => import('@/components/userCenter/member-content')
            },
          ]
        },
        //用户个人中心
        {
          showName: "个人中心",
          path: '/userCenter',
          name: '',
          component: () => import('@/components/userCenter/personal-center'),
          children: [
            {
              showSecondName: '用户中心',
              path: '',
              name: 'user_0',
              component: () => import('@/components/userCenter/personal-default')
            },
            //用户注册/修改密码/忘记密码
            {
              showSecondName: '账号中心',
              path: '/user_1',
              name: 'user_1',
              component: () => import('@/components/userCenter/register/user-register')
            }
          ]
        }
      ]
    },
    {
      path: '/',
      redirect: contain,
    },
  ]
})

export default router



