<!--
 * @Description:文章评论页
 * @Author: your name
 * @Date: 2019-07-09 16:02:39
 * @LastEditTime: 2019-07-14 19:44:02
 * @LastEditors: Please set LastEditors
 -->
<template>
  <div id="comment">
    <div id="statement">
      声明： 本文由入驻公众服务平台的作者攒写，观点仅代表作者本人，不代表平台立场。如果有侵权或其他问题，请联系举报。
    </div>
    <div class="comment_top">
      <div class="comment_outline">
        <div>
          <h4>我来说两句</h4>
          <div class="comment_data">（共
            <a>
              <strong>0</strong>
            </a>条评论，<a> <strong>0</strong></a>
            人参与）</div>
        </div>
        <div class="acount_right">
          <div class="comment_exit">[退出]</div>
          <div class="comment_account">用户xxx | </div>
        </div>
      </div>
      <textarea placeholder="文明社会，理性评论"></textarea>
      <button class="sendcomment" src="#">发布评论</button>
    </div>
    <div class="comment_bottom">
      <div class="comment_show_top">
        <ul class="comment_classify_btn">
          <li><a @click="handleclick1" :class="[{'title-active':isOn1}]">最新评论</a></li>
          <li><a @click="handleclick2" :class="[{'title-active':isOn2}]">热门评论</a></li>
        </ul>
      </div>
      <div class="comment_show_down">
        <p>暂无评论</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isOn1: true,
      isOn2: false
    };
  },
  methods: {
    handleclick1: function() {
      this.isOn1 = true;
      this.isOn2 = false;
    },
    handleclick2: function() {
      this.isOn2 = true;
      this.isOn1 = false;
    }
  }
};
</script>

<style lang="scss" scoped>
$blue: #127de7;
$blue_light: #5093d6;
h4 {
  font: bold 21px/30px "Microsoft YaHei", "微软雅黑", SimHe, "黑体";
  float: left;
  color: #333;
  margin: 0;
  padding-left: 2px;
}
.comment_top {
  textarea {
    box-sizing: border-box;
    resize: none;
    width: 100%;
    padding: 0;
    top: 0;
    outline: none;
    min-height: 100px;
    max-height: 100px;
    font-size: 12px;
    border-bottom: solid 1px #ccc;
    outline: none;
    display: block;
    height: 34px;
    padding: 6px 12px;
    font-size: 14px;
    line-height: 1.42858;
    color: #555;
    background-color: #fff;
    background-image: none;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  }

  button {
    margin-top: 10px;
    float: right;
    margin-bottom: 0;
    font-weight: normal;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
    background-image: none;
    border: 1px solid transparent;
    white-space: nowrap;
    padding: 6px 12px;
    font-size: 14px;
    line-height: 1.42858;
    border-radius: 4px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    color: #fff;
    background-color: $blue;
    border-color: $blue_light;
  }
}
.acount_right {
  float: right;
}
.comment_data {
  padding: 0 0 0 5px;
  color: #333;
  font-size: 12px;
  a {
    line-height: 40px;
    text-align: left;
    font: 12px "瀹嬩綋";
    &:hover {
      text-decoration: underline;
    }
  }
}
strong {
  font-family: Verdana;
  font-style: italic;
  font-weight: normal;
  font-size: 17px;
  color: $blue !important;
}

.comment_outline {
  & div {
    display: inline-block;
    margin: 5px 3px;
    vertical-align: top;
  }
  .comment_account {
    float: right;
    font-size: 14px;
    vertical-align: middle;
  }
}
.comment_exit {
  font-size: 14px;
  vertical-align: middle;
  float: right;
  color: $blue;
  &:hover {
    color: $blue;
    text-decoration: underline;
  }
}
.comment_bottom {
  margin-top: 50px;
  width: 100%;
}
.comment_show_top {
  //background:#a49f9f2b;
  display: block;
  width: 100%;
  height: 45px;
  line-height: 30px;
  ul {
    height: inherit;
    //height:30px;
    //line-height: 30px;
    width: 100%;
    height: 34px;
    line-height: 33px;
    border-bottom: 1px solid #ddd;
  }
  li {
    float: left;
    height: inherit;
    line-height: inherit;
    padding: 6px;
    //border:0.2px solid #d3d0d0;
    border-bottom: none;
    cursor: pointer;
    margin-right: 1em;
    font-family: microsoft yahei;
    font-size: 15px;
  }
}
.title-active {
  position: relative;
}
.comment_show_down {
  & {
    margin: 0;
    padding: 2px 10px;
  }
  p{
    margin: 0;
  }
}
#statement {
  color: gray;
  padding: 10px;
  border-top: solid 2px black;
  border-top: solid 2px #000;
  margin-top: 20px;
  padding: 10px 0;
  color: #999;
  line-height: 20px;
  color: #999;
  line-height: 20px;
  font-size: 12px;
  text-align: left;
}
</style>



