<!--
 * @Description: 用户登录框
 -->
<template>
  <!-- 用户中心（登录状态） -->
  <div id="userLogin">
    <el-col class="warp-main" v-loading="loading" element-loading-text="拼命加载中">
      <div class="wrap">
        <form class="login">
          <p class="title">登录</p>
          <input v-model="account" type="text" placeholder="account" autofocus />
          <i class="fa fa-user"></i>
          <input v-model="password" type="password" placeholder="Password" />
          <i class="fa fa-key"></i>
          <a class="forget" @click="routerLink()"> 忘记密码？</a>
          <a class="remember"><input type="checkbox" name="checkbox" v-model="checked" id='remember'>
            记住密码</a>
          <button @click="submit($event)">
            <!-- <i class="spinner"></i> -->
            <span class="state">{{rank==0?'登录':'退出登录'}}</span>
          </button>
        </form>
      </div>
      <div v-if="rank" class="wrapper"></div>
    </el-col>
  </div>
</template>
<script>
import Qs from "qs";
import eventBus from "@/common/eventBus";

export default {
  name: "user-login",
  // props: ["username"],
  data() {
    return {
      rank: global.userItem.rank,
      account: "", //账号
      password: "", //密码
      checked: false, //记住登录
      loading: false //登录加载
    };
  },
  methods: {
    routerLink() {
      let type= 2
      let path = "/user_1";
      this.$router.push({ path: path, query: { type: type } });
    },
    //本地存储记住账号、密码
    rememberPass() {
      let strName = this.account;
      let strPass = this.password;
      localStorage.setItem("robotKeyName", strName);
      if (this.checked) {
        localStorage.setItem("robotKeyPass", strPass);
      } else {
        localStorage.removeItem("robotKeyPass");
      }
    },
    //请求
    submit(e) {
      e.preventDefault();
      if (!this.rank) this.login();
      if (this.rank) this.logout();
    },
    //登录
    login() {
      let account = this.account;
      let password = this.password;
      let that = this;
      let url = this.HOST + "/user/login";
      let params = Qs.stringify({
        username: account, //String用户名
        password: password //String密码
      });
      //启动loading动画
      that.loading = true;
      that
        .axios({
          method: "post",
          url: url,
          data: params
        })
        .then(response => {
          that.loading = false;
          if (response.data.result == "error") {
            let message = response.data.message;
            that.$message.error({
              showClose: true,
              message: message,
              duration: 2000
            });
          } else {
            that.rememberPass();
            global.userItem.rank = response.data.message.role; //储存权限信息
            that.rank = response.data.message.role;
            global.userItem.userName = account; //更新本地存储的用户名
            that.$message.success({
              showClose: true,
              message: "登录成功",
              duration: 2000
            });
            that.$emit("userSignIn", that.account);
            that.status = "退出登录";
            // that.ok = true;
            //后接其他操作
          }
        })
        .catch(response => {
          // console.log(response);
          that.loading = false;
          that.$message.error({
            showClose: true,
            message: "请求出现异常",
            duration: 2000
          });
        });
    },
    //登出
    logout() {
      let that = this;
      that
        .$confirm("确定退出?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
        .then(() => {
          that.axios.get(that.HOST + "/user/loginOut").then(response => {
            global.userItem.rank = 0;
            that.rank = 0;
          });
          setTimeout(function() {
            that.init();
          }, 300);
        })
        .catch(() => {});
    },
    //初始化
    init() {
      let that = this;
      //检测有无记住我
      if (localStorage.getItem("robotKeyName")) {
        that.account = localStorage.getItem("robotKeyName");
      }
      if (localStorage.getItem("robotKeyPass")) {
        that.checked = true;
        that.password = localStorage.getItem("robotKeyPass");
      }
    }
  },
  mounted() {
    this.init();
  },
  created() {
    let that = this;
    eventBus.$on("SynchronizeRank", function(val) {
      that.rank = val;
    });
    eventBus.$on("SynchronizeLogout", function() {
      that.logout();
    });
  },
  watch: {}
};
</script>
<style lang="scss" scoped>
$primary: #2196f3;

#userLogin {
  position: relative;
  font-family: "Open Sans", sans-serif;
  height: 100%;
  width: 100%;
  overflow: hidden;
}

// @keyframes spinner {
//   0% {
//     transform: rotateZ(0deg);
//   }
//   100% {
//     transform: rotateZ(359deg);
//   }
// }

* {
  box-sizing: border-box;
}

.wrap {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  min-height: 100%;
}

.login {
  border-radius: 2px 2px 5px 5px;
  padding: 5px 20px 5px 20px;
  width: 90%;
  max-width: 380px;
  background: #ffffff;
  position: relative;
  padding-bottom: 80px;
  // box-shadow: 0px 1px 5px rgba(0,0,0,0.3);

  // &.loading {
  //   button {
  //     max-height: 100%;
  //     padding-top: 50px;
  //     // .spinner {
  //     //   opacity: 1;
  //     //   top: 40%;
  //     // }
  //   }
  // }

  // &.ok {
  //   button {
  //     background-color: #c3584a;
  // .spinner {
  //   border-radius: 0;
  //   border-top-color: transparent;
  //   border-right-color: transparent;
  //   height: 20px;
  //   animation: none;
  //   transform: rotateZ(-45deg);
  // }
  //   }
  // }

  input {
    display: block;
    padding: 15px 10px;
    margin-bottom: 10px;
    width: 100%;
    border: 1px solid #ddd;
    transition: border-width 0.2s ease;
    border-radius: 2px;
    color: #ccc;

    & + i.fa {
      color: #fff;
      font-size: 1em;
      position: absolute;
      margin-top: -47px;
      opacity: 0;
      left: 0;
      transition: all 0.1s ease-in;
    }

    &:focus {
      & + i.fa {
        opacity: 1;
        left: 30px;
        transition: all 0.25s ease-out;
      }
      outline: none;
      color: #444;
      border-color: $primary;
      border-left-width: 35px;
    }
  }

  a.forget {
    font-size: 0.8em;
    color: $primary;
    text-decoration: none;
    display: inline-block;
    cursor: pointer;
  }

  a.remember {
    float: right;
    font-size: 0.8em;
    color: #444;
  }

  #remember {
    display: inline-block;
    width: auto;
    cursor: pointer;
  }

  .title {
    color: #444;
    font-size: 1.2em;
    font-weight: bold;
    margin: 10px 0 10px 0;
    border-bottom: 1px solid #eee;
    padding-bottom: 13px;
  }

  button {
    width: 88%;
    height: 15%;
    padding: 10px 10px;
    margin: 10px auto;
    background: $primary;
    color: #fff;
    display: block;
    border: none;
    position: absolute;
    left: 6%;
    bottom: 8%;
    max-height: 60px;
    border: 0px solid rgba(0, 0, 0, 0.1);
    border-radius: 0 0 2px 2px;
    transform: rotateZ(0deg);
    transition: all 0.1s ease-out;
    border-bottom-width: 7px;
    z-index: 4;
    cursor: pointer;
    // .spinner {
    //   display: block;
    //   width: 40px;
    //   height: 40px;
    //   position: absolute;
    //   border: 4px solid #ffffff;
    //   border-top-color: rgba(255, 255, 255, 0.3);
    //   border-radius: 100%;
    //   left: 50%;
    //   top: 0;
    //   // opacity: 0;
    //   margin-left: -20px;
    //   margin-top: -20px;
    //   animation: spinner 0.6s infinite linear;
    //   transition: top 0.3s 0.3s ease, opacity 0.3s 0.3s ease,
    //   border-radius 0.3s ease;
    //   box-shadow: 0px 1px 0px rgba(0, 0, 0, 0.2);
    // }

    &:hover {
      box-shadow: 0px 1px 3px $primary;
    }
    &:focus {
      border-bottom-width: 4px;
    }
    &:active {
      height: 14%;
      bottom: 8.5%;
      background: rgb(33, 139, 226);
      border: 1px solid rgb(12, 76, 87);
      -webkit-box-shadow: 0px 1px 6px 4px rgba(121, 121, 121, 0.2) inset;
      -moz-box-shadow: 0px 1px 6px 4px rgba(138, 136, 136, 0.2) inset;
      box-shadow: 0px 1px 6px 4px rgba(126, 123, 123, 0.2) inset;
    }
  }
}

.wrapper {
  background-color: #e9e9e9;
  width: 100%;
  height: 100%;
  top: 0;
  position: absolute;
  clear: both;
  z-index: 3;
  opacity: 0.5;
}

input:-webkit-autofill,
input[type="text"]:focus,
input[type="password"]:focus {
  -webkit-box-shadow: 0 0 0 1000px white inset;
  box-shadow: 0 0 0 1000px white inset;
}
</style>



