<!--
 * @Description: 文章详情页
 -->
 <template>
  <div id="content—container">
    <!--左边长列表-->
    <div class="box1-left">
      <currentLocate></currentLocate>
      <div class="box2-left">
        <component :is="currentType" :queryProp="query"></component>
      </div>
      <div class="box8-left">
        <relate-read v-if="currentType=='main-content'"></relate-read>
      </div>
      <div class="box11-left" v-if="currentType=='main-content' && query.message.name!='政策'">
        <comment></comment>
      </div>
    </div>
    <!--右边长列表-->
    <div class="box4-right">
      <div class="box5-right">
        <keep-alive>
          <pb-list :widgetMessage="tabs2[1]" :widgetType="1"></pb-list>
        </keep-alive>
      </div>
      <div class="box6-right">
        <user-box></user-box>
      </div>
      <div class="box7-right">
        <keep-alive>
          <pb-list :widgetMessage="tabs2[0]" :widgetType="1"></pb-list>
        </keep-alive>
      </div>
      <div class="box12-right">
        <ul class="title">
          <li class="list_title">
            <a style="margin-left:15px;" class="title-active">广告招收栏目</a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <slide2></slide2>
          </keep-alive>
        </div>
      </div>
    </div>
  </div>
</template>
 <script>
import UserBox from "@/components/common/tools/user-box";
import slide2 from "@/components/common/tools/slide2";
import currentLocate from "@/components/common/tools/currentLocate";
import PbList from "@/components/common/tools/pb-list";
import comment from "@/components/index/content/comment";
import relateRead from "@/components/index/content/relate-read";
import mainContent from "@/components/index/content/main-content";
import mainContentTalent from "@/components/index/content/main-content-talent";
import mainContentProduct from "@/components/index/content/main-content-product";
import { proofreading } from "@/api/getData";

export default {
  name: "contentContainer",
  data() {
    return {
      query: JSON.parse(this.$route.query.queryMsg) //利用JSON传输，避免刷新后出现[Object,Object]的情况
    };
  },
  computed: {
    tabs2() {
      let value = global.tabs;
      let value1, value2;
      value1 = value.filter(item => {
        if (item.belong == "活动") return true;
      });
      value2 = value.filter(item => {
        if (item.belong == "相关热点") return true;
      });
      if (!value[0].message && !value[0].message && !value[0].message) {
        proofreading.call(this); //数据未加载，调用函数校对加载
      }
      value = [value1, value2];
      return value;
    },
    currentType() {
      switch (this.query.message.name) {
        case "专家库":
          return "main-content-talent";
          break;
        case "高校":
          return "main-content-talent";
          break;
        case "协会会员":
          return "main-content-talent";
          break;
        case "产品库":
          return "main-content-product";
          break;
        case "热门产品":
          return "main-content-product";
          break;
        default:
          return "main-content";
          break;
      }
    }
    // query: {
    //   get() {
    //     if (this.$route.query.queryMsg) {
    //       return JSON.parse(this.$route.query.queryMsg); //利用JSON传输，避免刷新后出现[Object,Object]的情况
    //     } else {
    //       return {};
    //     }
    //   },
    //   set(newVal) {}
    // }
  },
  methods: {
    init() {
      scroll(0, 300);
      //根据传参判断路由显示路径
      let localName = this.query.message.name;
      let localMenu = this.query.message.belong;
      let index = this.$router.options.routes[0].children.findIndex(
        menu => menu.showName == localMenu
      );
      let index2;
      global.currentPath.path = localName;
      // console.log(localMenu);
      if (localMenu == "新闻热点") {
        global.currentPath.path = localMenu + "-" + localName;
      }
      if (index != -1) {
        index2 = this.$router.options.routes[0].children[
          index
        ].children.findIndex(menu => menu.showSecondName == localName);
        global.currentPath.data = [index, index2];
      } else {
        global.currentPath.data = [index];
      }
      // if (
      //   (localMenu == "会议" || localMenu == "展会") &&
      //   localName != "展会动态" &&
      //   localName != "会议动态"
      // ) {
      //   global.currentPath.data = [-1];
      //   global.currentPath.path = localMenu + "-" + localName;
      // }
    }
  },
  activated() {
    this.query = JSON.parse(this.$route.query.queryMsg);
    this.init();
  },
  beforeRouteUpdate(to, from, next) {
    this.query = JSON.parse(to.query.queryMsg);
    this.init();
    next();
  },
  components: {
    UserBox,
    slide2,
    PbList,
    comment,
    relateRead,
    mainContent,
    mainContentTalent,
    mainContentProduct,
    currentLocate
  }
};
</script>
<style lang="scss" scoped>
#content—container /deep/ img {
  max-width: 650px;
}
@mixin adv {
  margin: 5px auto;
  width: 1200px;
  height: 125px;
  overflow: hidden;
}
@mixin w-box {
  margin: 5px auto;
  width: 1200px;
  height: 312px;
  overflow: hidden;
  margin-bottom: 18px;
}
@mixin i-box {
  box-shadow: 0 0 1px #bebebe;
  height: 100%;
  overflow: hidden;
  border-radius: 2px;
}
@mixin border {
  -webkit-box-shadow: 0 0 1px #bebebe;
  box-shadow: 0 0 1px #bebebe;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border: 1px solid rgba(204, 204, 204, 0.377);
}
#content—container {
  @include adv;
  height: 100%;
}
/*右边小框重复样式*/
@mixin litter_one_tab_box {
  @include i-box;
  @include border;
  height: 312px;
  width: 390px;
  float: right;
  margin-bottom: 10px;
}
.hot_search {
  @include adv;
  height: 100%;
  //margin-left:15px;
  padding: 13px;
  background: rgba(255, 255, 255, 0.95);
}
.box1-left {
  margin-top: 20px;
  width: 775px;
  float: left;
  @include i-box;
  padding: 13px;
  background: rgba(255, 255, 255, 0.95);
  div {
    width: 775px; //迫于无奈改的
  }
}
.box2-left {
  //@include i-box;
  //height: 50px;
  margin: 10px 0;
}

/*左边文章样式*/
.box3-left {
  //@include i-box;
  height: 100%;
  margin: 10px 0;
  background: rgba(255, 255, 255, 0.95);
}
.box4-right {
  margin-top: 20px;
  width: 390px;
  //@include i-box;
  float: right;
}
.box4-right div {
  margin-right: 0.5px;
}
.box12-right,
.box5-right,
.box6-right,
.box7-right {
  @include litter_one_tab_box;
  background: rgba(255, 255, 255, 0.95);
}
.box8-left {
  //@include i-box;
  float: left;
  width: 795px;
  margin: 10px 0;
}
.box9-left {
  @include i-box;
  float: left;
  width: 795px;
  height: 250px !important;
  margin: 10px 0;
}
.box10-left {
  //@include i-box;
  float: left;
  width: 795px;
  height: 50px;
  margin: 10px 0;
}
.box11-left {
  //@include i-box;
  float: left;
  width: 795px;
  height: 400px;
  margin: 10px 0;
}
//行业报告的形式
.image {
  margin: 0 auto;
  width: 200px;
  margin-left: 300px;
}
.Industry_Report_span_style {
  float: right;
  margin-right: 200px;
}
.Industry_Report_span0_style {
  float: right;
}
#Industry_Report_a {
  text-decoration: underline;
  color: #127de7;
}
#Industry_Report_a:hover {
  text-decoration: underline;
  color: red;
}
.title {
  margin-bottom: 10px;
  margin-top: 2px;
  width: 100%;
  background: #fff;
  height: 32px;
  line-height: 28px;
}
</style>



