<!--
 * @Description: 显示产品详细内容的组件
 -->
 <template>
  <div id="main-content-Product">
    <div class="left leftBox">
      <h1 id="bigTitle">{{!loading?message.name:'数据正在加载... loading...'}}</h1>
      <div id="infoBox-product" v-if="message">
        <div class=textBox>
          <span class="type">类型: </span>
          <div class="content" v-text="message.type"></div><br>
          <span class="type">品牌: </span>
          <div class="content" v-text="message.brand"></div><br>
          <span class="type">样式: </span>
          <div class="content" v-text="message.pattern"></div><br>
          <span class="type">产地: </span>
          <div class="content" v-text="message.residence"></div><br>
          <span class="type">价格: </span>
          <div class="content" v-text="message.price"></div><br>
          <span class="type">发布日期: </span>
          <div class="content" v-text="message.lastUpdateTime"></div><br>
          <span class="type">有效期至: </span>
          <div class="content" v-text="message.effectTime"></div><br>
        </div>
        <img id="productPic" style="width: 350px;" :src='message.img'>
      </div>
      <div v-if="loading">
        <noContent :loadName="'数据'"></noContent>
      </div>
      <div class="flexCenter">
        <vue-editor v-if="message" class="editor1 innerContent" v-html="message.introduction" :editorToolbar="[]" :disabled=true></vue-editor>
      </div>
    </div>
  </div>
</template>
 <script>
import { VueEditor } from "vue2-editor";
import noContent from "@/components/index/content/no-content";
import { getMainContent } from "@/api/getData";
import eventBus from "@/common/eventBus";

export default {
  name: "mainContentProduct",
  props: ["queryProp"],
  data() {
    return {
      message: "",
      loading: true,
      queryMsg: "",
      id: ""
    };
  },
  methods: {
    //获取对象详细信息
    getData(url, id) {
      scroll(0, 300);
      let that = this;
      getMainContent(url, id)
        .then(res => {
          that.loading = false;
          if (!res.message) return;
          that.message = res.message;
        })
        .catch(err => {
          that.loading = false;
          that.$message.error({
            showClose: true,
            message: "获取详细信息失败",
            duration: 2000
          });
        });
    }
  },
  activated() {
    this.message = null;
  },
  watch: {
    queryProp: {
      handler(val) {
        if (val) {
          this.queryMsg = this.queryProp.message;
          this.id = this.queryProp.id; //获取对象id

          let hostUrl = this.HOST;
          if (this.queryMsg.newsUrl) {
            hostUrl += this.queryMsg.newsUrl; //拼接请求路径
            this.loading = true;
            this.getData(hostUrl, this.id); //调用获取对象信息方法
          }
        }
      },
      immediate: true,
      deep: true
    }
  },
  components: { noContent, VueEditor }
};
</script>
<style lang="scss" scope>
div#mainContent >>> * {
  max-width: 100%;
  white-space: pre-wrap;
}
div.left {
  width: 100%;
  padding: 0 15px;
  margin-bottom: 15px;
  box-sizing: border-box;
  display: inline-block;
}
div#mainContent {
  background: #fafafa;
  border: 1px solid #f1f1f1;
}
h1#bigTitle {
  text-align: center;
  padding: 30px;
}
div#infoBox-product {
  padding: 10px 0 30px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
div#innerContent {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
div.apage {
  box-sizing: border-box;
  min-width: 51%;
  white-space: pre-line;
}
img#productPic {
  display: block;
  max-width: 90%;
  margin: 0 auto;
}
div#information {
  color: gray;
  position: relative;
  left: 2rem;
  padding: 10px;
}
div.textBox {
  text-align: center;
  padding: 10px;
}
div.content {
  display: inline-block;
}
span.type {
  display: inline-block;
  margin-left: 2rem;
  margin-top: 4px;
  margin-bottom: 4px;
  margin-right: 0.5rem;
  font-weight: bold;
}
</style>
<style>
.flexCenter .quillWrapper .ql-snow.ql-toolbar {
  display: none;
}
.flexCenter .ql-container.ql-snow {
  border: none;
}
</style>



