<!--
 * @Description: 显示专家高校内容的组件
 -->
 <template>
  <div id="main-content-talent">
    <div class="left leftBox">
      <h1 id="bigTitle">{{!loading?message.name:'数据正在加载... loading...'}}</h1>
      <div id="infoBox" v-if="message">
        <span>发表时间 ：{{message.postDate||'未知'}}</span>
      </div>
      <div v-if="loading">
        <noContent :loadName="'数据'"></noContent>
      </div>
      <div class="flexCenter">
        <vue-editor v-if="message" class="editor1 innerContent" v-html="message.introduction" :editorToolbar="[]" :disabled=true></vue-editor>
      </div>
    </div>
  </div>
</template>
 <script>
import { VueEditor } from "vue2-editor";
import noContent from "@/components/index/content/no-content";
import { getMainContent } from "@/api/getData";
import eventBus from "@/common/eventBus";

export default {
  name: "mainContentTalent",
  props: ["queryProp"],
  data() {
    return {
      message: "",
      loading: true,
      pages: 0,
      currentPage: 1,
      queryMsg: "",
      id: ""
    };
  },
  methods: {
    //获取对象详细信息
    getData(url, id) {
      scroll(0, 300);
      let that = this;
      getMainContent(url, id)
        .then(res => {
          that.loading = false;
          if (!res.message) return;
          that.message = res.message;
        })
        .catch(err => {
          that.loading = false;
          that.$message.error({
            showClose: true,
            message: "获取详细信息失败",
            duration: 2000
          });
        });
    }
  },
  activated() {
    this.message = null;
  },
  watch: {
    queryProp: {
      handler(val) {
        if (val) {
          this.queryMsg = this.queryProp.message;
          this.id = this.queryProp.id; //获取对象id

          let hostUrl = this.HOST;
          if (this.queryMsg.newsUrl) {
            hostUrl += this.queryMsg.newsUrl; //拼接请求路径
            this.loading = true;
            this.getData(hostUrl, this.id); //调用获取对象信息方法
          }
        }
      },
      immediate: true,
      deep: true
    }
  },
  components: { noContent, VueEditor }
};
</script>
<style lang="scss" scope>
div#mainContent >>> * {
  max-width: 100%;
  white-space: pre-wrap;
}
div.left {
  width: 100%;
  padding: 0 15px;
  margin-bottom: 15px;
  box-sizing: border-box;
  display: inline-block;
}
div#mainContent {
  background: #fafafa;
  border: 1px solid #f1f1f1;
}
h1#bigTitle {
  text-align: center;
  padding: 30px;
}
div#infoBox {
  display: flex;
  justify-content: space-around;
  padding: 30px 0;
}
div#innerContent {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
div.apage {
  box-sizing: border-box;
  min-width: 51%;
  white-space: pre-line;
}

</style>
<style>
.flexCenter .quillWrapper .ql-snow.ql-toolbar {
  display: none;
}
.flexCenter .ql-container.ql-snow {
  border: none;
}


/* 内容 */
.news_right {
  width: 100% !important;
  position: relative;
  border: none !important;
}
.xxy_dbt6 {
  display: none !important;
  font-size: 24px;
  color: #0071cf;
  display: block;
  text-align: center;
  padding-top: 30px;
  padding-bottom: 10px;
}
.xxy_ht2 {
  color: #000000;
  font-size: 14px;
  height: 40px;
  display: block;
  background: #f6f6f6;
  text-align: center;
  line-height: 40px;
  margin: 15px 15px;
}
.xxy_ht2 span {
  padding-left: 5px;
  padding-right: 6px;
}
.xxnr {
  display: block;
  line-height: 26px;
  font-size: 14px;
  color: #5b5b5b;
  margin: 10px 5px;
}
</style>



