<!--
 * @Description: 显示文章内容的组件
 -->
 <template>
  <div id="main-content">
    <!-- 普通格式 -->
    <div class="left leftBox" v-if="queryMsg.name!='技术研讨'&&queryMsg.name!='行业报告'&&queryMsg.belong!='会议'&&queryMsg.belong!='展会'">
      <h1 id="bigTitle">{{!loading?message.title:'数据正在加载... loading...'}}</h1>
      <div id="infoBox" v-if="message">
        <span>发表时间 ：{{message.postDate||'未知'}}</span>
        <span>来源 ：{{message.source||'未知'}}</span>
        <span>阅读量 ：{{message.viewCount||'未知'}}</span>
      </div>
      <div v-if="loading">
        <noContent :loadName="'数据'"></noContent>
      </div>
      <div class="flexCenter">
        <vue-editor v-if="content" class="editor1 innerContent" v-html="content[currentPage-1].content" :editorToolbar="[]" :disabled=true></vue-editor>
      </div>
    </div>
    <!-- 行业报告格式 -->
    <div class="left leftBox" v-if="queryMsg.name=='行业报告'">
      <h1 id="bigTitle">{{!loading?message.title:'数据正在加载... loading...'}}</h1>
      <div v-if="loading">
        <noContent :loadName="'数据'"></noContent>
      </div>
      <div class="flexCenter reportBox" v-if="message">
        <div class="reportMsg">
          <div class="coverImg">
            <img :src="message.img">
          </div>
          <div class="guide">
            <span class="reportKey">导读</span>
            <span class="value">{{message.guide}}</span>
          </div>
          <div class="reportBoxLeft">
            <span class="reportKey">发布者</span>
            <span class="value">{{message.editor}}</span>
          </div>
          <div class="reportBoxRight">
            <span class="reportKey">首次出版时间</span>
            <span class="value">{{message.firstPostDate}}</span>
          </div>
          <div class="reportBoxLeft">
            <span class="reportKey">最新发布时间</span>
            <span class="value">{{message.postDate}}</span>
          </div>
          <div class="reportBoxRight">
            <span class="reportKey">交付方式</span>
            <span class="value">{{message.delivery}}</span>
          </div>
          <div class="reportBoxLeft">
            <span class="reportKey">报告页数</span>
            <span class="value">{{message.reportPage}}</span>
          </div>
          <div class="reportBoxRight">
            <span class="reportKey">报告字数</span>
            <span class="value">{{message.reportNum}}</span>
          </div>
          <div class="reportBoxLeft">
            <span class="reportKey">报告所属公司</span>
            <span class="value">{{message.production}}</span>
          </div>
          <div class="reportBoxRight">
            <span class="reportKey">图表数量</span>
            <span class="value">{{message.graphNum}}</span>
          </div>
          <div class="reportBoxBlock">
            <span class="reportKey">所属专业领域</span>
            <span class="value">{{message.industry}}</span>
          </div>
          <div class="reportBoxBlock">
            <span class="reportKey">原文链接</span>
            <span class="value">{{message.url}}</span>
          </div>
        </div>
        <vue-editor v-if="content" class="editor1 innerContent" v-html="content[currentPage-1].content" :editorToolbar="[]" :disabled=true></vue-editor>
      </div>
    </div>
    <!-- 技术研讨格式 -->
    <div class="left leftBox" v-if="queryMsg.name=='技术研讨'">
      <h1 id="bigTitle">{{!loading?message.title:'数据正在加载... loading...'}}</h1>
      <div v-if="loading">
        <noContent :loadName="'数据'"></noContent>
      </div>
      <div class="flexCenter" v-if="message">
        <div class="discussMsg">
          <div class="coverImg">
            <img :src="message.img">
          </div>
          <div class="contentVideo">
            <video controls="controls">
              <source :src="message.video" type="video/mp4" />
              <source :src="message.video" type="video/ogg" />
              <source :src="message.video" type="video/webm" />
              <object :data="message.video">
                <embed :src="message.video" />
              </object>
            </video> </div>
        </div>
      </div>
    </div>
    <!-- 普通格式 -->
    <div class="left leftBox" v-if="queryMsg.belong=='会议'||queryMsg.belong=='展会'">
      <h1 id="bigTitle">{{!loading?message.title:'数据正在加载... loading...'}}</h1>
      <div id="infoBox" v-if="message">
        <div class=textBox>
          <span class="type">举办时间: </span>
          <div class="content" v-text="message.postDate"></div><br>
          <span class="type">地址: </span>
          <div class="content" v-text="message.address"></div><br>
        </div>
        <img id="productPic" style="width: 350px;" :src='message.coverImg'>
      </div>
      <div v-if="loading">
        <noContent :loadName="'数据'"></noContent>
      </div>
      <div class="flexCenter">
        <vue-editor v-if="message" class="editor1 innerContent" v-html="message.introduction" :editorToolbar="[]" :disabled=true></vue-editor>
      </div>
    </div>
  </div>
</template>
 <script>
import { VueEditor } from "vue2-editor";
import noContent from "@/components/index/content/no-content";
import { getMainContent } from "@/api/getData";
import eventBus from "@/common/eventBus";

export default {
  name: "mainContent",
  props: ["queryProp"],
  data() {
    return {
      message: "",
      content: "",
      loading: true,
      pages: 0,
      currentPage: 1,
      queryMsg: "",
      id: ""
    };
  },
  methods: {
    //获取文章详细信息
    getData(url, id) {
      scroll(0, 300);
      let that = this;
      getMainContent(url, id)
        .then(res => {
          that.loading = false;
          if (!res.message) return;
          if (that.queryMsg.name == "技术研讨") {
            that.message = {
              title: res.message.title,
              video: res.message.content,
              img: res.message.img
            };
          } else if (
            that.queryMsg.belong == "会议" ||
            that.queryMsg.belong == "展会"
          ) {
            that.message = res.message;
            that.message.postDate = res.message.postDate.slice(0, 10);
          } else {
            that.message = res.message.information;
            that.message.title = that.message.title || that.message.name;
            that.content = that.message.content;
            if (typeof that.content == "string")
              that.content = [{ content: that.content }]; // that.content为content数据的数组对象，每个代表一页
            //传递相关阅读的值给兄弟组件
            let val = Object.create(null);
            val.message = res.message.related.information;
            if (val.message) {
              val.url = url;
              eventBus.$emit("getRelatedData", val); //同步到相关阅读数据
            }
          }
        })
        .catch(err => {
          that.loading = false;
          that.$message.error({
            showClose: true,
            message: "获取文章详情失败",
            duration: 2000
          });
        });
    }
  },
  mounted() {
    let that = this;
    //点击相关阅读后，更新该页面信息
    eventBus.$on("freshData", function(url, id) {
      that.loading = true;
      that.getData(url, id); //调用获取文章信息方法
    });
  },
  activated() {
    this.message = null;
  },
  watch: {
    queryProp: {
      handler(val) {
        if (val) {
          this.queryMsg = this.queryProp.message;
          this.id = this.queryProp.id; //获取文章id

          let hostUrl = this.HOST;
          if (this.queryMsg.newsUrl) {
            hostUrl += this.queryMsg.newsUrl; //拼接请求路径
            this.loading = true;
            this.getData(hostUrl, this.id); //调用获取文章信息方法
          }
        }
      },
      immediate: true,
      deep: true
    }
  },
  components: { noContent, VueEditor }
};
</script>
<style lang="scss" scope>
div#mainContent >>> * {
  max-width: 100%;
  white-space: pre-wrap;
}
div.left {
  width: 100%;
  padding: 0 15px;
  margin-bottom: 15px;
  box-sizing: border-box;
  display: inline-block;
}
div#mainContent {
  background: #fafafa;
  border: 1px solid #f1f1f1;
}
h1#bigTitle {
  text-align: center;
  padding: 30px;
}
div#infoBox {
  display: flex;
  justify-content: space-around;
  padding: 30px 0;
}
div#innerContent {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
div.apage {
  box-sizing: border-box;
  min-width: 51%;
  white-space: pre-line;
}

.reportBox {
  & {
    display: flex;
    flex-direction: column;
    justify-items: center;
    align-items: center;
    padding: 10px 0;
    margin-top: 20px;
  }
  .reportMsg {
    & {
      color: #666;
      padding: 20px;
    }
    .coverImg {
      & {
        display: flex;
        flex-direction: column;
        align-items: center;
      }
      img {
        width: 220px;
      }
    }
    .guide {
      & {
        padding: 20px;
        line-height: 1.3;
      }
      .value {
        color: rgb(75, 75, 75);
      }
    }
    .reportBoxLeft,
    .reportBoxBlock {
      padding-left: 20px;
      padding-top: 5px;
    }
    .reportBoxLeft,
    .reportBoxRight {
      padding-top: 5px;
      width: 49%;
      display: inline-block;
      box-sizing: border-box;
    }
    .reportKey {
      margin: 5px;
    }
    .value {
      color: rgb(37, 37, 37);
    }
  }
}
.discussMsg {
  & {
    padding: 20px;
  }
  .coverImg {
    & {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-bottom: 40px;
    }
    img {
      width: 400px;
    }
  }
  .contentVideo {
    & {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-bottom: 40px;
    }
    video {
      max-width: 95%;
    }
  }
}
</style>
<style>
.flexCenter .quillWrapper .ql-snow.ql-toolbar {
  display: none;
}
.flexCenter .ql-container.ql-snow {
  border: none;
}
</style>



