<!--
 * @Description: 产品库列表
 -->
<template>
  <div id="main-list-product">
    <!-- 加载框 -->
    <div v-if="loading">
      <h1 id="bigTitle">列表正在加载... loading...</h1>
      <noContent :loadName="'列表'"></noContent>
    </div>
    <!-- 存放列表数据的框 -->
    <div v-show="!loading" id="list-box">
      <!-- 列表 -->
      <div class="lists">
        <li v-for="(item,index) in currentList" :key="index" class="list">
          <div class="contentPart">
            <div class="imgCover">
              <img :src="item.img" @click="routerLink(currentList[index].id)">
            </div>
            <div class="info">
              <h2 @click="routerLink(currentList[index].id)">
                {{item.name}}
              </h2>
              <div class="price">
                价格：{{item.price}}
              </div>
            </div>
          </div>
        </li>
      </div>
      <!-- 分页组件 -->
      <div class="pagination">
        <el-pagination @current-change="handleCurrentChange" background :current-page.sync="currentPage" :page-size="pageSize" layout="prev, pager, next, jumper" :total="total">
        </el-pagination>
      </div>
    </div>

  </div>
</template>
<script>
import { getProductList } from "@/api/getData";
import noContent from "@/components/index/content/no-content";

export default {
  props: ["queryProp"],
  name: "main-list-product",
  data() {
    return {
      queryMsg: "",
      currentList: "",
      total: 0,
      pageSize: 12,
      currentPage: 1,
      hostUrl: "",
      loading: true
    };
  },
  methods: {
    routerLink: function(id) {
      let query = new Object();
      query.id = id;
      query.message = this.queryMsg;
      let path = "content";
      delete query.message.message; //删除不必要的数据
      let data = JSON.stringify(query); ///利用JSON传输，避免刷新后出现[Object,Object]的情况
      this.$router.push({ path: path, query: { queryMsg: data } });
    },
    //获取列表
    getData() {
      let that = this;
      let url = this.hostUrl;
      let pageNum = that.currentPage;
      that.loading = true;
      // scroll(0, 300);
      getProductList(url, pageNum)
        .then(res => {
          let data = res.message;
          if (data) {
            that.loading = false;
            that.currentList = data.list;
            that.total = data.total;
            that.currentPage = data.pageNum;
            that.pageSize = data.pageSize;
          }
        })
        .catch(err => {
          that.loading = false;
          that.$message.error({
            showClose: true,
            message: "获取文章列表错误",
            duration: 2000
          });
        });
    },
    //更改页数
    handleCurrentChange(val) {
      this.currentPage = val;
      this.getData();
    }
  },
  watch: {
    queryProp: {
      handler(val) {
        if (val) {
          this.queryMsg = this.queryProp.message;

          if (this.queryMsg.requestPath) {
            this.hostUrl = this.HOST + this.queryMsg.requestPath; //拼接请求路径
            this.getData(); //调用获取文章列表方法
          }
        }
      },
      immediate: true,
      deep: true
    }
  },
  components: { noContent }
};
</script>

<style lang="scss" scoped>
$blue: #127de7;
.title:hover > a {
  color: $blue;
}
#main-list-product {
  margin-top: 16px;
  background: #f6f6f6;
  border-radius: 2px;
}
h1#bigTitle {
  text-align: center;
  padding: 30px;
}
.list {
  box-sizing: border-box;
  border-bottom: dashed 1px #ccc;
  -webkit-box-shadow: 0 0 1px #bebebe;
  box-shadow: 0 0 1px #bebebe;
  width: 100%;
  display: inline-block;
  padding: 25px 25px;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.contentPart {
  .imgCover {
    & {
      display: inline-block;
      margin-right: 20px;
    }
    img {
      height: 120px;
      width: 120px;
      vertical-align: middle;
      cursor: pointer;
    }
  }
  .info {
    & {
      display: inline-block;
    }
    h2 {
      & {
        font-size: 18px;
        font-weight: bold;
        line-height: 24px;
        height: 24px;
        margin-bottom: 4px;
        color: #3d3d3d;
        word-wrap: break-word;
        word-break: break-all;
        overflow: hidden;
        text-overflow: ellipsis;
        display: inline-block;
      }
      &:hover {
        cursor: pointer;
        color: $blue;
      }
    }
    .price {
      margin-top: 5px;
    }
  }
}

.pagination {
  padding: 20px 0;
}
</style>



