import axios from 'axios'
import context from '../pages/index/index.js'//引入全局vue对象
import Qs from "qs";

// const service = axios.create({
//   baseURL: context.HOST,
//   timeout: 200000,
// });

//获取登录信息
export function getLoginData(prefix) {
  const url = prefix + '/user/getPermission'
  return axios({
    method: 'get',
    url: url
  }).then((res) => {
    return Promise.resolve(res.data)
  }).catch((err) => {
    return Promise.reject(err.data)
  })
}

//普通用户注册相关
//用户点击获取验证码
export function userValidate(formData, url) {
  // const url = '/user/validate'
  return axios({
    method: 'post',
    url: context.HOST + url,
    data: formData,
    headers: { 'Content-Type': 'multipart/form-data' },
  }).then((res) => {
    return Promise.resolve(res.data)
  }).catch((err) => {
    return Promise.reject(err.data)
  })
}

//用户提交注册验证码确认提交
export function userRegister(code, url) {
  // const url = '/user/register'
  let params = Qs.stringify({
    checkCode: code
  });
  return axios({
    method: 'post',
    url: context.HOST + url,
    data: params,
  }).then((res) => {
    return Promise.resolve(res.data)
  }).catch((err) => {
    return Promise.reject(err.data)
  })
}

//用户提交重置密码验证码确认提交
export function userForget(code, url) {
  // const url = '/user/register'
  let params = {
    checkCode: code
  }
  return axios({
    method: 'get',
    url: context.HOST + url,
    params: params,
  }).then((res) => {
    return Promise.resolve(res.data)
  }).catch((err) => {
    return Promise.reject(err.data)
  })
}


