export function getMemberList(obj) {
  let host = obj ? obj.HOST : context.HOST
  console.log(host)
  const url = '/user/getMemberList'
  return axios({
    method: "get",
    url: host + url,
  }).then((res) => {
    return Promise.resolve(res.data)
  }).catch((err) => {
    return Promise.reject(err.data)
  })
}

//协会审批
export function approvalMember(data) {
  const url = context.HOST + '/user/judgeMember'
  let params = Qs.stringify(data);
  return axios({
    method: "post",
    url:url,
    data: params
  }).then(res => {
    return Promise.resolve(res.data)
  }).catch((err) => {
    return Promise.reject(err.data)
  })
}



