<template>
  <div id="x_related_reading">
    <div class="x_related_reading_title">
      <p>相关阅读：</p>
      <li v-for="(item,index) of list" :key="index" class="relate-list">
        <div class="relate-list-content" @click.stop='dataFresh(item.id)'>{{item.title}}</div>
        <div class="relate-list-time">{{item.postDate||''}}</div>
      </li>

    </div>
  </div>
</template>

<script>
import eventBus from "@/common/eventBus";
export default {
  data() {
    return {
      list: [],
      url:''
    };
  },
  methods:{
    dataFresh(id){
      eventBus.$emit("freshData",this.url,id)
    }
  },
  mounted() {
    let that = this;
    //从mainContent处获取数据
    eventBus.$on("getRelatedData", function(val) {
      that.list = val.message;
      that.url = val.url
    });
  }
};
</script>

<style lang="scss" scoped>
$blue: #127de7;
$blue_light: #5093d6;
#x_related_reading {
  padding: 5px;
}
.relate-list {
  margin: 10px;
  list-style: disc;
  text-overflow: ellipsis;
  &:hover {
    color: $blue;
    cursor: pointer;
  }
}
p {
  font-weight: bold;
}
.relate-list-content,
.relate-list-time {
  display: inline-block;
}
.relate-list-content {
  text-overflow: ellipsis;
}
.relate-list-time {
  float: right;
  font-weight: 600;
}
</style>



